/*

reform page

*/

body.reform{

	.l-page_hero {
		&::before {
			background-image: url(../img/reform/hero_01.jpg);
		}
	}

	.p-page_renovate {
		position: relative;
		display: flex;

		&__inner {
			width: $width_site;
			max-width: 1080px;
			margin: 0 auto;
			padding: 80px 0;
			position: relative;
			z-index: 2;
		}

		&__head {
			max-width: 670px;
		}

		&__title {
			.elem_en {
				@include rem(font-size, 7.0rem);
				font-family: $montserrat;
				font-weight: 700;
				color: #E6E6E6;
				line-height: 1;
				margin-bottom: 30px;
			}
		}

		&__image {
			width: 38%;
			max-width: 470px;
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			z-index: 1;
		}

		.p-btn {
			margin-top: 40px;
		}
	}
}
