.js-animateblock {
	transition: filter .6s , opacity .5s;
	opacity: 0;
	filter: brightness(160%);

	&.is-animated {
		opacity: 1;
		filter: brightness(100%);
	}

	&--image {
		transition: opacity .5s, transform .6s;
		transform: scale(1.3);
		opacity: 0;
		transform-origin: center center;

		&.is-animated {
			transform: scale(1.0);
			opacity: 1;
		}
	}

	&--scale {
		transition: opacity .3s, transform .6s cubic-bezier(.17,.67,.58,1.25);
		transform: scale(.5);
		opacity: 0;
		transform-origin: center bottom;

		&.is-animated {
			transform: scale(1.0);
			opacity: 1;
		}
	}
}

//一文字つづ現れるエフェクト
.splitting {
	.char {
		transition: transform 1s cubic-bezier(0,.96,.1,1),opacity 2s cubic-bezier(0,.96,.1,1);
		transition-delay: calc( 60ms * var(--char-index) );
		transform: scale(1.2);
		display: inline-block;
		opacity: 0;

		&.is-animated {
			transform: scale(1);
			opacity: 1;
		}
	}
}