.l-site_footer{
	background-color: $color_main;
	color: #fff;
	padding: 80px 0 25px;
	position: relative;

	@include mq(sp) {
		padding: 60px 20px 30px;
		padding-bottom: 54px;
	}

	&__inner{
		margin: 0 auto;
		position: relative;

		@include mq(liquid, true) {
			width: $width_site;
			max-width: 1080px;
		}

		a {
			color: #fff;
		}
	}

	&__nav {
		margin-bottom: 40px;

		@include mq(tablet, true) {
			display: flex;
		}

		&__head {
			@include mq(tablet, true) {
				width: 25%;
				display: flex;
				justify-content: space-between;
				flex-direction: column;
			}

			@include mq(tablet) {
				text-align: center;
				margin-bottom: 60px;
			}

			@include mq(sp) {
				margin-bottom: 0;
			}

			.logo {
				@include mq(tablet) {
					width: 60%;
					margin: 0 auto 30px;
				}
			}

			.p-btn {
				@include mq(tablet) {
					width: 50%;
					margin: 0 auto;
				}

				@include mq(sp) {
					width: 100%;
				}
			}

		}

		&__list {
			width: 75%;
			display: flex;

			@include mq(tablet) {
				width: 100%;
			}

			@include mq(sp) {
				display: none;
			}

			> * {
				width: 33%;
				padding: 0 40px;

				@include mq(tablet) {
					padding: 0 20px;
				}

				+ ul {
					border-left: 1px solid #fff;
				}
			}

			a {
				position: relative;
				left: 0;
				transition: left .1s;
				&:hover {
					left: 10px;
					text-decoration: none;
				}
			}

			&__lv1 {
				> li {
					> a {
						display: block;
						padding: 5px 0;
						@include rem(font-size, 1.4rem);
						position: relative;

						@include mq(tablet) {
							@include rem(font-size, 1.2rem);
						}
						&::before {
							margin-right: 10px;
							@include rem(font-size, 1.8rem);
							vertical-align: middle;

							@include mq(tablet) {
								@include rem(font-size, 1.4rem);
								margin-right: 5px;
							}
						}
					}
				}
			}

			&__lv2{
				padding-left: 2em;
				> li {
					> a {
						display: block;
						padding: 3px 0;
						@include rem(font-size, 1.4rem);

						@include mq(tablet) {
							@include rem(font-size, 1.2rem);
						}

						&::before {
							content: "-";
							margin-right: 10px;
							vertical-align: middle;
							position: relative;
							top: -1px;
						}
					}
				}
			}
		}
	}

	&__extra {
		@include mq(sp, true) {
			display: flex;
			justify-content: space-between;
		}
		@include mq(sp) {
			text-align: center;
		}
	}

	&__privacy {
		@include mq(sp) {
			margin-bottom: 10px;
		}
		a {
			@include rem(font-size, 1.2rem);
			font-weight: 300;
		}
	}


	&__copyright{
		text-align: right;
		@include rem(font-size, 1.2rem);
		opacity: 0.5;
		font-weight: 300;

		@include mq(sp) {
			@include rem(font-size, 1.0rem);
			text-align: center;
		}
	}
}

.page_top{
	position:fixed;
	right: -50px;
	bottom: 30px;
	transition: right .2s;
	z-index: 3;

	@include mq(sp) {
		bottom: 60px;
	}

	&.is_active {
		right: 0;
	}

	&.is_static {
		position: absolute;
		top: -80px;
		right: 0;
		bottom: auto;
	}

	a{
		display:block;
		width: 50px;
		height: 50px;
		text-align:center;
		color:#fff;
		background-color: $color_main;
		position: relative;
		transition: background-color .3s, border-color .3s;
		border: 2px solid $color_main;
		border-radius: 10px 0 0 10px;

		&::after {
			content: "";
			display: block;
			width: 11px;
			height: 11px;
			border-left: 4px solid #fff;
			border-top: 4px solid #fff;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%,-50%) rotate(45deg);
			margin-top: 4px;
			transition: margin-top .3s;
		}

		&:hover {
			background-color: #fff;
			&::after {
				margin-top: -2px;
				border-color: $color_main;
			}
		}
	}
}
