.p-heading_lv1,
.p-heading_lv2,
.p-heading_lv3,
.p-heading_lv4{
	font-feature-settings: "palt";
	letter-spacing: .1em;
}
.p-heading_lv1{
	@include rem(font-size, 3.2rem);
	font-weight: 700;
	line-height: 1.6;
	letter-spacing: 10px;

	@include mq(sp) {
		@include rem(font-size, 2.4rem);
		margin-bottom: 20px;
		letter-spacing: 5px;
	}
}

.p-heading_lv2{
	@include rem(font-size, 3.6rem);
	margin-bottom: 60px;
	font-weight: 600;
	border-left: 3px solid $color_main;
	padding-left: 30px;
	line-height: 1.6;

	@include mq(sp) {
		@include rem(font-size, 2.4rem);
		margin-bottom: 20px;
		padding-left: 20px;
	}

	&.reform {
		border-left-color: $color_reform;
	}

	+ p {
		padding-left: 35px;

		@include mq(sp) {
			padding-left: 0;
		}
	}
}

.p-heading_lv3{
	@include rem(font-size, 2.8rem);
	margin-bottom: 25px;
	padding-bottom: 20px;
	line-height: 1.6;
	font-weight: 600;
	position: relative;
	border-bottom: 2px solid #E6E6E6;
	letter-spacing: 2px;

	@include mq(tablet) {
		@include rem(font-size, 2.4rem);
	}

	&::before{
		content: "";
		display: block;
		width: 60px;
		height: 2px;
		background-color: #457B9D;
		position: absolute;
		bottom: -2px;
		left: 0;
	}

	b {
		color: $color_main;
		letter-spacing: 0
	}

	&.karitai {
		&::before {
			background-color: $color_karitai;
		}
	}

	&.kashitai {
		&::before {
			background-color: $color_kashitai;
		}
	}
	&.uritai {
		&::before {
			background-color: $color_uritai;
		}
	}
	&.kaitai {
		&::before {
			background-color: $color_kaitai;
		}
	}
	&.reform {
		&::before {
			background-color: $color_reform;
		}
	}
}

.p-heading_lv4{
	@include rem(font-size, 2.4rem);
	font-weight: 600;
	line-height: 1.6;
	margin-bottom: 20px;

	@include mq(tablet) {
		@include rem(font-size, 1.8rem);
	}

	@include mq(sp) {
		@include rem(font-size, 1.6rem);
	}
}

.p-heading_hero {
	@include rem(font-size, 3.2rem);
	font-weight: 700;
	line-height: 1.6;
	letter-spacing: 10px;

	@include mq(sp) {
		@include rem(font-size, 2.8rem);
		letter-spacing: 6px;
	}

	i {
		width: 75px;
		height: 75px;
		line-height: 75px;
		color: #fff;
		background-color: $color_main;
		display: block;
		text-align: center;
		border-radius: 50%;
		letter-spacing: 0;
		margin-bottom: 40px;

		@include mq(sp) {
			margin: 0 auto 20px;
		}

		&::before {
			@include rem(font-size, 4.6rem);
			line-height: 75px;
		}

		&.p-icon {
			&--karitai {
				background-color: $color_karitai;
			}
			&--kashitai {
				background-color: $color_kashitai;
			}
			&--uritai {
				background-color: $color_uritai;
			}
			&--kaitai {
				background-color: $color_kaitai;
			}
			&--reform {
				background-color: $color_reform;
			}
		}
	}

	span {
		display: block;
	}
}

.p-sub_heading {
	.elem {
		&_en {
			font-weight: 700;
			@include rem(font-size, 2.6rem);
			line-height: 1.4;
			letter-spacing: 2px;
			font-family: $montserrat;
			color: $color_main;
		}
		&_jp {
			@include rem(font-size, 1.4rem);
		}
	}
}