.p-pagination {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 30px;

	&__list {
		display: flex;
		justify-content: center;

		@include mq(sp) {
			flex-wrap: wrap;
		}
	}

	&__prev,
	&__next {
		margin: 0 30px;

		@include mq(sp) {
			margin: 0 10px;
		}

		&.disable {
			a {
				color: #E6E6E6;
				pointer-events: none;
			}
		}
		a {
			@include rem(font-size, 4.0rem);
			color: $color_main;
			line-height: 1;
			height: auto;
			width: auto;
			position: relative;
			transition: left .2s, right .2s;

			@include mq(sp) {
				@include rem(font-size, 3.0rem);
			}

			&:hover {
				text-decoration: none;
			}
		}
	}

	&__next {
		a {
			left: 0;
			&:hover {
				left: 10px;
			}
		}
	}

	&__prev {
		a {
			right: 0;
			&:hover {
				right: 10px;
			}
		}
	}

	&__item {
		margin: 0 5px;

		@include mq(sp) {
			margin: 10px 10px;
		}

		&.current {
			a {
				background-color: $color_main;
				color: #fff;
				pointer-events: none;
			}
		}
		a {
			display: block;
			border-radius: 50%;
			width: 28px;
			height: 28px;
			line-height: 28px;
			text-align: center;
			font-family: $montserrat;
			transition: background-color .2s, color .2s;

			@include mq(sp) {
				width: 38px;
				height: 38px;
				line-height: 38px;
			}

			&:hover {
				text-decoration: none;
				background-color: $color_main;
				color: #fff;
			}
		}
	}

	&.reform {
		.p-pagination__prev,
		.p-pagination__next {
			a {
				color: $color_reform;
			}
			&.disable {
				a {
					color: #E6E6E6;
				}
			}
		}

		.p-pagination__item {
			&.current {
				a {
					background-color: $color_reform;
				}
			}

			a {
				&:hover {
					background-color: $color_reform;
				}
			}
		}
	}
}