.topic_path {
	position: absolute;
	background-color: #F1F1F1;
	border-radius: 0 100px 100px 0;
	top: -.5em;
	left: 0;
	padding-left: 10%;
	padding-right: 10px;

	@include mq(sp) {
		top: -21px;
		padding-left: 0;
		overflow-x: auto;
		max-width: 100%;
		-webkit-overflow-scrolling : touch;
	}

	a {
		color: $color_main;
		padding: 5px 10px;
		display: block;
	}

	ul {
		display: flex;
		align-items: center;

		@include mq(sp) {
			white-space: nowrap;
		}

		li {
			@include rem(font-size, 1.2rem);
			position: relative;
			padding: 5px 10px;
			margin-right: 3px;
			color: #ccc;

			@include mq(sp) {
				@include rem(font-size, 1.1rem);
				padding: 5px 5px;
				margin-right: 2px;
				letter-spacing: 0;
			}

			&::after {
				content: "";
				width: 10px;
				height: 1px;
				background-color: $color_main;
				position: absolute;
				right: 0px;
				top: 50%;

				@include mq(sp) {
					width: 5px;
					right: -3px;
				}
			}

			&:last-of-type {
				&::after {
					display: none;
				}
			}
		}
	}

}