/*

top page

*/

body.home{

	.l-home_hero {
		overflow-x: hidden;
		margin-bottom: 50px;

		@include mq(sp) {
			margin-bottom: 30px;
		}

		.slick {
			img {
				width: 100%;
				vertical-align: top;
			}

			.slick-slide {
				margin: 0 20px;
				border-radius: 20px;
				overflow: hidden;
			}
		}
	}

	.l-home_section {
		.p-btn {
			a {
				@include mq(sp) {
					min-width: 170px;
				}
			}
		}
	}

	.p-home_information {
		position: relative;

		@include mq(tablet, true) {
			margin-bottom: 80px;
		}

		@include mq(tablet) {
			background-color: #F5F5F5;
		}

		&::before {
			@include mq(tablet, true) {
				content: "";
				display: block;
				width: 94%;
				height: 100%;
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				background-color: #F5F5F5;
				border-radius: 0 300px 300px 0;
			}
		}

		&__inner {
			@include mq(sp, true) {
				display: flex;
				justify-content: space-between;
			}
		}

		&__head {
			@include mq(tablet, true) {
				width: 26%;
			}

			@include mq(tablet) {
				width: 40%;
			}

			@include mq(sp) {
				width: 100%;
				margin-bottom: 10px;
			}
		}

		&__body {
			@include mq(tablet, true) {
				width: 74%;
			}
		}

		&__item {
			+ .p-home_information__item {
				border-top: 1px solid $color_line;
			}
			a {
				display: block;
				width: 100%;
				align-items: center;

				@include mq(sp, true) {
					display: flex;
					padding: 5px 0;
				}

				@include mq(sp) {
					padding: 10px 0;
				}

				&:hover {
					text-decoration: none;
					.title {
						color: $color_main;
					}
				}
			}
			.time {
				@include rem(font-size, 1.2rem);
				font-family: $montserrat;
				margin-right: 25px;

				@include mq(sp) {
					color: #959595;
					display: block;
					margin-bottom: 5px;
				}
			}
			.title {
				transition: color .3s;

				@include mq(sp) {
					line-height: 1.5;
					display: block;
				}
			}
		}
	}

	.p-home_philosophy {
		background-image: url(../img/home/bg_philosophy_01.jpg);
		background-size: 100% auto;
		background-position: center bottom;
		background-repeat: no-repeat;

		@include mq(tablet, true) {
			padding: 100px 0;
		}

		@include mq(sp) {
			background-image: url(../img/home/bg_philosophy_01_sp.jpg);
		}

		&__inner {
			@include mq(sp, true) {
				display: flex;
				justify-content: space-between;
			}
		}

		&__head {
			@include mq(sp, true) {
				width: 38%;
			}

			.heading {
				.elem {
					&_en {
						@include rem(font-size, 1.8rem);
						font-weight: 700;
						line-height: 1.4;
						font-family: $montserrat;
						color: $color_main;
						position: relative;
						display: inline-block;

						@include mq(tablet) {
							@include rem(font-size, 1.4rem);
						}

						&::after {
							content: "";
							display: block;
							width: 80px;
							height: 3px;
							background-color: $color_main;
							position: absolute;
							right: -100px;
							top: 50%;
							transform: translateY(-50%);
							margin-top: -1px;

							@include mq(tablet) {
								height: 2px;
								width: 60px;
								right: -70px;
							}
						}
					}

					&_jp {
						@include rem(font-size, 6.4rem);
						position: relative;
						left: -10px;
						margin-top: 25px;
						letter-spacing: 6px;
						font-weight: 600;

						@include mq(tablet) {
							letter-spacing: 3px;
							@include rem(font-size, 4.5rem);
						}

						span {
							display: block;
							line-height: 1.4;

							&:first-child {
								color: $color_main;
							}

							&:last-of-type {
								color: $color_karitai;
							}
						}
					}
				}
			}

			.lead {
				margin-top: 30px;
				line-height: 2;

				@include mq(sp) {
					margin-top: 20px;
				}
			}
		}

		&__body {
			@include mq(sp, true) {
				width: 54%;
			}

			@include mq(sp) {
				margin-top: 40px;
			}
		}

		&__list {
			@include mq(sp, true) {
				display: flex;
				flex-wrap: wrap;
			}
		}

		&__item {
			@include mq(sp, true) {
				width: 49%;
				margin-left: 2%;
				margin-bottom: 2%;
			}

			@include mq(sp) {
				height: 180px;
				margin-bottom: 20px;
			}

			&:nth-child(odd) {
				@include mq(sp, true) {
					margin-left: 0;
				}
			}

			a {
				@include mq(sp) {
					display: block;
					height: 100%;
				}
				&::before {
					@include rem(font-size, 3.4rem);
					padding: 15px 15px 30px 30px;

					@include mq(tablet) {
						@include rem(font-size, 2.4rem);
					}

					@include mq(sp) {
						@include rem(font-size, 3.4rem);
					}
				}
				figure {
					img {
						@include mq(sp) {
							object-fit: cover;
							width: 100%;
							height: 100%;
						}
					}
				}
				figcaption {
					@include rem(font-size, 2.2rem);
					padding: 10px 35px 5px 30px;

					@include mq(tablet) {
						padding: 10px 15px 5px 10px;
						@include rem(font-size, 2.0rem);
					}

					@include mq(sp) {
						padding: 10px 25px 5px 20px;
					}
				}
			}

			&.reform {
				margin-left: auto;
				margin-right: 0;
			}
		}
	}

	.p-home_voice {
		background-image: url(../img/home/bg_voice_01.jpg);
		background-size: auto 100%;
		background-position: left center;
		background-repeat: no-repeat;
		padding: 7% 0;
		margin-top: 7%;

		@include mq(sp) {
			margin-top: 40px;
			background-image: url(../img/home/bg_voice_01_sp.jpg);
		}

		&__inner {
			position: relative;
		}

		&__head {
			color: #fff;

			@include mq(tablet,true) {
				position: absolute;
				left: 0;
				top: 0;
				z-index: 2;
			}

			.heading {
				.elem {
					&_en {
						@include mq(tablet,true) {
							margin-left: -130px;
						}

						@include mq(tablet) {
							width: 60%;
						}

						@include mq(sp) {
							margin-left: -20px;
							width: 80%;
						}
					}

					&_jp {
						@include rem(font-size, 2.2rem);
						margin-top: 30px;

						@include mq(sp) {
							@include rem(font-size, 2.0rem);
						}
					}
				}
			}

			.lead {
				margin-top: 15px;
			}

			.p-btn {
				margin-top: 30px;
			}
		}

		&__list {
			@include mq(sp,true) {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
			}
		}

		&__item {
			margin-top: 7%;

			@include mq(tablet) {
				width: 48%;
			}

			@include mq(sp) {
				width: 100%;
				margin-top: 60px;
			}

			&:first-child {
				@include mq(tablet, true) {
					margin-left: 34%;
					margin-top: 3%;
				}
			}
			&:nth-child(2) {
				@include mq(tablet, true) {
					margin-top: 3%;
				}
			}
		}
	}

	.p-home_news {
		position: relative;
		margin-top: 5%;
		padding: 0;

		@include mq(sp) {
			margin-top: 40px;
			margin-bottom: 40px;
		}

		&::before{
			@include mq(tablet, true) {
				content: "";
				display: block;
				border: $color_main 6px solid;
				border-right: none;
				position: absolute;
				right: 0;
				top: 50%;
				z-index: 1;
				transform: translateY(-50%);
				width: 50%;
				height: calc(100% - 12px);
				background-color: #fff;
			}
		}

		&__inner {
			background-color: #fff;

			@include mq(tablet, true) {
				padding: 50px 50px;
				display: flex;
				justify-content: space-between;
				border-radius: 20px 0 0 20px;
				border: $color_main 6px solid;
				border-right: none;
				position: relative;
				z-index: 2;
				width: $width_site_wide;
				max-width: 1200px;
			}
		}

		&__head {
			@include mq(tablet, true) {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
			}

			.elem_en {
				@include rem(font-size, 3.6rem);
				line-height: 1.2;
				letter-spacing: 0;

				@include mq(sp) {
					@include rem(font-size, 2.4rem);
				}
			}

			.elem_jp {
				@include rem(font-size, 1.8rem);
				margin-top: 10px;

				@include mq(sp) {
					@include rem(font-size, 1.4rem);
					margin-top: 5px;
				}
			}
		}

		&__body {
			@include mq(tablet, true) {
				width: 78%;
			}

			@include mq(tablet) {
				margin-top: 40px;
			}
		}

		&__list {
			@include mq(sp, true) {
				display: flex;
				justify-content: space-between;
			}
		}

		&__item {
			@include mq(sp, true) {
				width: 30%;
			}

			@include mq(sp) {
				border-bottom: 1px solid $color_line;
				padding-bottom: 20px;
				margin-bottom: 20px;
			}

			a {
				@include mq(sp) {
					display: flex;
					justify-content: space-between;
					align-items: flex-start;
				}

				&:hover {
					text-decoration: none;
					.p-home_news__photo {
						img {
							left: -10px;
							top: -10px;
						}
					}

					.p-home_news__content {
						.title {
							color: $color_main;
						}
					}
				}
			}
		}

		&__photo {
			position: relative;
			background-color: $color_main;
			border-radius: 10px;

			@include mq(sp) {
				width: 47%;
			}

			img {
				position: relative;
				border-radius: 10px;
				left: 0;
				top: 0;
				transition: left .2s, top .2s;
			}
		}

		&__content {
			margin-top: 15px;

			@include mq(sp) {
				margin-top: 0;
				width: 50%;
			}

			.time {
				color: #959595;
				@include rem(font-size, 1.2rem);
				font-family: $montserrat;
			}

			.title {
				@include rem(font-size, 1.4rem);
				transition: color .3s;
			}

			.lead {
				margin-top: 5px;
				@include rem(font-size, 1.3rem);

				@include mq(sp) {
					margin-top: 3px;
					line-height: 1.4;
				}
			}
		}
	}

	.p-home_extra {
		margin-top: 2%;

		@include mq(sp) {
			background-color: #F1F1F1;
		}

		&__list {
			@include mq(sp, true) {
				display: flex;
				justify-content: space-between;
			}
		}
		&__item {
			text-align: center;
			border-radius: 20px;
			overflow: hidden;
			background-size: cover;
			@include mq(sp, true) {
				width: 31%;
			}

			@include mq(sp) {
				margin-top: 20px;
			}

			&:first-child{
				@include mq(sp) {
					margin-top: 0;
				}
			}

			a {
				display: block;
				color: #fff;
				position: relative;
				border: #f1f1f1 6px solid;
				transition: border-color .3s;
				overflow: hidden;
				border-radius: 20px;
				background-color: $color_text;

				@include mq(sp) {
					height: 160px;
				}

				&:hover {
					text-decoration: none;
					border-color: $color_main;

					img {
						opacity: 0.8;
					}
				}

				figure {
					overflow: hidden;
				}

				.title {
					font-weight: 600;
					@include rem(font-size, 2.0rem);
					position: absolute;
					left: 50%;
					top: 50%;
					width: 100%;
					transform: translate(-50%,-50%);

					@include mq(sp) {
						@include rem(font-size, 1.8rem);
					}

					&::before {
						@include rem(font-size, 4.6rem);
						display: block;
						margin-bottom: 15px;
						font-weight: 400;
					}
				}

				img {
					opacity: 0.6;
					transition: opacity .3s;
					border-radius: 15px;
					overflow: hidden;

					@include mq(sp) {
						object-fit: cover;
						width: 100%;
						height: 100%;
					}
				}
			}
		}
	}

}
