/*

ボタン

*/

.p-btn {
	position: relative;

	.inner {
		position: relative;
	}

	&:hover {
		.inner {
			&::before {
				transform: scaleX(1);
			}
		}
	}

	a,b,input {
		color: $color_text;
		padding: 12px 20px;
		position: relative;
		display: block;
		font-weight: 600;
		width: 100%;
		z-index: 2;
		transition: padding .2s, border-color .2s, background-color .2s, color .2s;

		@include mq(sp) {
			padding: 8px 20px;
		}

		&:hover {
			text-decoration: none;
		}

		i {
			position: absolute;
			top: 50%;
			right: 40px;
			transform: translateY(-50%);
		}
	}

	&--more {
		margin-top: 15px;
		a,b,input {
			color: $color_text;
			padding: 5px 10px;
			position: relative;
			display: inline-block;
			font-weight: 600;
			z-index: 2;
			transition: padding .2s, border-color .2s, background-color .2s, color .2s;
			border-bottom: 2px solid $color_main;
			@include rem(font-size, 1.4rem);
			color: $color_main;

			@include mq(sp) {
				padding: 8px 20px;
			}

			&:hover {
				text-decoration: none;

				i {
					margin-left: 35px;
				}
			}

			i {
				margin-left: 25px;
				transition: margin-left .2s;
			}
		}

		&.white {
			a,b,input {
				color: inherit;
				border-bottom-color: inherit;
			}
		}
	}

	&--primary {
		max-width: 310px;
		min-width: 310px;
		position: relative;
		&::before {
			color: #fff;
			position: absolute;
			right: 20px;
			top: 50%;
			transform: translateY(-50%);
			z-index: 2;
			transition: transform .2s;
		}

		&::after {
			content: "";
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0%;
			top: 0%;
			//transform: translate(-50%, -50%);
			background-color: $color_main;
			border-radius: 100px;
			transition: transform .2s;
		}

		&:hover {
			&::after {
				transform: scale(1.1);
			}

			i {
				right: 15px;
			}
		}

		a,b,input {
			border: none;
			color: #fff;
			padding: 20px 40px;
			cursor: pointer;
			display: block;
			background-color: transparent;
			text-align: center;
			@include rem(font-size, 1.5rem);

			i {
				right: 25px;
				transition: right .2s;
			}
		}
	}

	&--secondary {
		position: relative;
		&::before {
			color: #fff;
			position: absolute;
			right: 20px;
			top: 50%;
			transform: translateY(-50%);
			z-index: 2;
			transition: transform .2s;
		}

		&::after {
			content: "";
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0%;
			top: 0%;
			//transform: translate(-50%, -50%);
			background-color: $color_main;
			border-radius: 100px;
			transition: transform .2s;
			border: #fff 2px solid;
		}

		&:hover {
			&::after {
				transform: scale(1.1);
			}
		}

		a,b,input {
			border: none;
			color: #fff;
			padding: 10px 20px;
			cursor: pointer;
			display: block;
			background-color: transparent;
			text-align: center;
		}
	}

	&--border {
		position: relative;
		&::before {
			position: absolute;
			right: 20px;
			top: 50%;
			transform: translateY(-50%);
			z-index: 2;
			transition: transform .2s;
		}

		&::after {
			content: "";
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			left: -1px;
			top: -1px;
			//transform: translate(-50%, -50%);
			border-radius: 100px;
			transition: transform .2s;
			border: $color_main 2px solid;
		}

		&:hover {
			&::after {
				transform: scale(1.1);
			}
		}

		a,b,input {
			border: none;
			color: $color_main;
			padding: 15px 20px;
			cursor: pointer;
			display: block;
			background-color: transparent;
			text-align: center;
		}
	}

	&--black {
		background-color: #1A1A1A;
		color: #fff;
		border-radius: 100px;
		text-align: center;
		@include rem(font-size, 1.4rem);
		font-family: inherit;
		font-weight: 500;
		padding: 10px 15px;
		border: none;
		cursor: pointer;
	}

	&--search {
		position: relative;
		max-width: 400px;
		margin: 40px auto 0;

		&::before {
			color: #fff;
			position: absolute;
			right: 20px;
			top: 50%;
			transform: translateY(-50%);
			z-index: 2;
			transition: transform .2s;
		}

		&::after {
			content: "";
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0%;
			top: 0%;
			//transform: translate(-50%, -50%);
			background-color: #F7E733;
			border-radius: 100px;
			transition: transform .2s;
		}

		&:hover {
			&::after {
				transform: scale(1.1);
			}
		}

		a,b,input {
			border: none;
			padding: 15px 20px;
			cursor: pointer;
			display: block;
			background-color: transparent;
			text-align: center;
		}
	}

}