.c-pagenavi {
	a {
		border: $color_main 5px solid;
		border-radius: 20px;
		display: block;
		overflow: hidden;
		position: relative;
		background-color: $color_text;

		@include mq(sp) {
			height: 200px;
		}

		&::before {
			color: #fff;
			position: absolute;
			right: 0;
			top: 0;
			background-color: $color_main;
			@include rem(font-size, 3.0rem);
			z-index: 2;
			padding: 12px 12px 25px 25px;
			border-radius: 0 0 0 300px;
			transition: padding .5s, font-size .5s;
		}

		&:hover {
			text-decoration: none;

			// &::before {
			// 	@include rem(font-size, 2.4rem);
			// }

			img {
				transform: scale(1.05);
				opacity: 1;
			}

			figure {
				&::after {
					opacity: 0;
				}
			}

			// figcaption {
			// 	padding: 10px 25px 5px 20px;
			// 	@include rem(font-size, 1.8rem);
			// }
		}

		img {
			transition: transform 3.0s, opacity .3s;
			opacity: 0.6;

			@include mq(sp) {
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}

		figure {
			&::after {
				content: "";
				display: block;
				width: 100%;
				height: 100%;
				background-color: rgba(#000, .4);
				position: absolute;
				left: 0;
				top: 0;
				transition: opacity 1.5s;
			}
		}

		figcaption {
			position: absolute;
			left: 0;
			bottom: 0;
			color: #fff;
			z-index: 2;
			background-color: $color_main;
			border-radius: 0 200px 0 0;
			@include rem(font-size, 1.8rem);
			text-align: center;
			padding: 5px 25px 3px 20px;
			font-weight: 600;
			letter-spacing: 2px;
			transition: padding .5s, font-size .5s;
		}
	}

	.mask {
		overflow: hidden;
	}

	&.karitai {
		a {
			border-color: $color_karitai;

			&::before {
				background-color: $color_karitai;
			}

			figcaption {
				background-color: $color_karitai;
			}
		}
	}

	&.kaitai {
		a {
			border-color: $color_kaitai;

			&::before {
				background-color: $color_kaitai;
			}

			figcaption {
				background-color: $color_kaitai;
			}
		}
	}

	&.kashitai {
		a {
			border-color: $color_kashitai;

			&::before {
				background-color: $color_kashitai;
			}

			figcaption {
				background-color: $color_kashitai;
			}
		}
	}

	&.uritai {
		a {
			border-color: $color_uritai;

			&::before {
				background-color: $color_uritai;
			}

			figcaption {
				background-color: $color_uritai;
			}
		}
	}

	&.reform {
		a {
			border-color: $color_reform;

			&::before {
				background-color: $color_reform;
			}

			figcaption {
				background-color: $color_reform;
			}
		}
	}
}
