.c-module_point {

	&__head {
		margin: 0 auto;

		@include mq(tablet, true) {
			width: $width_site;
			max-width: 1080px;
		}

		@include mq(tablet) {
			width: 90%;
		}
	}

	&__body {
		position: relative;
		margin-top: 40px;
		&::after {
			@include mq(tablet, true) {
				content: "";
				display: block;
				width: 50%;
				height: 100%;
				background-color: #fff;
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				z-index: 1;
			}
		}
	}

	&__wrap {
		background-color: #fff;
		margin: 0 auto;

		@include mq(tablet, true) {
			padding: 60px;
			width: $width_site_wide;
			max-width: 1200px;
			border-radius: 20px 0 0 20px;
		}

		@include mq(sp) {
			width: 90%;
			border-radius: 20px;
		}
	}

	&__inner {
		position: relative;
		z-index: 2;
		border-bottom: 1px solid $color_line;

		@include mq(tablet, true) {
			display: flex;
			justify-content: space-between;
			padding-bottom: 60px;
		}

		@include mq(tablet) {
			padding: 30px 0;
		}

		+ .c-module_point__inner {
			@include mq(tablet, true) {
				padding-top: 60px;
			}
		}

		&:last-of-type {
			border-bottom: none;
			padding-bottom: 0;
		}
	}

	&__icon {
		color: $color_main;
		font-family: $montserrat;
		font-weight: 700;
		line-height: 1;
		display: flex;
		align-items: center;
		justify-content: center;

		@include mq(tablet, true) {
			width: 12%;
			text-align: center;
			width: 126px;
			height: 126px;
			border-radius: 50%;
			border: $color_main 4px solid;
		}

		@include mq(tablet) {
			width: 160px;
			border: $color_main 3px solid;
			border-left: none;
			border-radius: 0 100px 100px 0;
			padding: 10px 0;
		}

		&.kashitai {
			border-color: $color_kashitai;
			color: $color_kashitai;
		}

		&.uritai {
			border-color: $color_uritai;
			color: $color_uritai;
		}

		&.kaitai {
			border-color: $color_kaitai;
			color: $color_kaitai;
		}

		&.reform {
			border-color: $color_reform;
			color: $color_reform;
		}

		b {
			display: block;
			@include rem(font-size, 4.2rem);

			@include mq(tablet) {
				display: inline-block;
				vertical-align: middle;
				@include rem(font-size, 3.6rem);
				margin-left: 5px;
			}
		}
	}

	&__column {
		@include mq(tablet, true) {
			width: 84%;
		}

		@include mq(tablet) {
			padding: 30px;
		}

		@include mq(sp) {
			padding: 0;
		}

		// .p-heading_lv3 {
		// 	@include mq(sp) {
		// 		width: 90%;
		// 		margin-top: 20px;
		// 		margin-right: auto;
		// 		margin-left: auto;
		// 	}
		// }

		&__inner {
			@include mq(sp, true) {
				display: flex;
				justify-content: space-between;
				padding-bottom: 40px;
			}

			+ .c-module_point__column__inner {
				padding-top: 40px;
				border-top: 1px dashed $color_line;
				padding-bottom: 0;
			}
		}

		&__content {
			@include mq(sp, true) {
				width: 59%;
			}

			@include mq(sp) {
				padding: 20px;
			}

			.point {
				li {
					counter-increment:decimal;
					display: flex;
					margin-bottom: 15px;

					&:last-of-type {
						margin-bottom: 0;
					}

					&::before {
						content: counter(decimal);
						font-family: $montserrat;
						width: 27px;
						height: 27px;
						display: inline-block;
						background-color: $color_main;
						color: #fff;
						line-height: 1;
						text-align: center;
						border-radius: 50%;
						line-height: 27px;
						margin-right: 10px;
					}

					dl {
						@include mq(sp) {
							width: 80%;
						}
					}

					dd {
						@include rem(font-size, 1.4rem);
					}
				}

				&.kaitai {
					li {
						&::before {
							background-color: $color_kaitai;
						}
					}
				}
			}
		}

		&__image {
			@include mq(sp, true) {
				width: 34%;
			}

			@include mq(sp) {
				width: 90%;
				margin: 0 auto;
			}

			img {
				border-radius: 20px;
				overflow: hidden;
			}
		}
	}


}