.p-table {
	width: 83%;
	margin: 70px auto 0;

	@include mq(sp) {
		width: 100%;
		margin-top: 40px;
		overflow-x: auto;
		-webkit-overflow-scrolling: touch;
	}

	&__form {
		width: 100%;
		border-top: 1px solid #E6E6E6;

		tbody {
			tr {
				border-bottom: 1px solid #E6E6E6;

				@include mq(sp) {
					display: block;
				}
			}

			th,td {
				vertical-align: middle;
				padding: 20px 0;

				@include mq(sp) {
					display: block;
				}
			}

			th {
				position: relative;
				text-align: left;

				@include mq(sp, true) {
					width: 38%;
					padding-left: 7%;
				}

				@include mq(sp) {
					padding-bottom: 0;
				}
			}

			td {
				@include mq(sp, true) {
					width: 62%;
					padding-left: 3%;
					padding-right: 7%;
				}
			}
		}

	}

	&__download {
		@include mq(sp) {
			width: 1000px;
		}
		th,td {
			border: #E6E6E6 1px solid;
		}
		thead {
			th {
				background-color: #F5F5F5;
				@include rem(font-size, 1.8rem);
				padding: 10px 20px;

				@include mq(sp) {
					@include rem(font-size, 1.6rem);
				}
			}
		}

		tbody {
			th,td {
				padding: 15px 20px;
				vertical-align: middle;

				@include mq(sp) {
					padding: 10px 15px;
				}
			}
			th {
				text-align: left;
				dl {
					display: flex;
					align-items: center;
					justify-content: space-between;
				}

				dt {
					width: 30%;
				}

				dd {
					@include rem(font-size, 1.4rem);
					font-weight: 500;
					width: 64%;
				}
			}
			td {
				width: 13%;
			}
		}
	}

	&__survey {
		width: 100%;
		tbody {
			tr,th,td {
				display: block;
			}

			tr {
				border-bottom: 1px dashed $color_line;
				padding: 25px 0;

				@include mq(sp) {
					padding: 20px 0;
				}
			}

			th {
				@include rem(font-size, 2.2rem);
				margin-bottom: 10px;
				text-align: left;

				@include mq(sp) {
					@include rem(font-size, 1.8rem);
				}
			}

			td {
				label {
					display: inline-block;
					margin-right: 20px;
					margin-bottom: 15px;

					input {
						margin-right: 10px;
					}
				}

				textarea {
					border: $color_line 1px solid;
				}

				b {
					display: block;
					@include rem(font-size, 1.8rem);
					margin-top: 20px;
					margin-bottom: 10px;
				}

				dt {
					border-left: 3px solid $color_main;
					padding-left: 10px;
					margin-top: 10px;
					margin-bottom: 10px;
				}
			}
		}
	}

}