/*

karitai page

*/

body.karitai{

	.l-page_hero {
		&::before {
			background-image: url(../img/karitai/hero_01.jpg);
		}
	}

	.p-page_choice {
		&__inner {
			@include mq(sp, true) {
				display: flex;
				flex-direction: row-reverse;
				align-items: center;
				justify-content: flex-end;
			}

			> * {
				@include mq(sp, true) {
					width: 48%;
				}
			}
		}

		&__head {
			@include mq(sp, true) {
				max-width: 510px;
			}
		}

		&__title {
			@include mq(sp) {
				width: 90%;
				margin: 0 auto;
			}
		}

		&__image {
			margin-right: 4%;
			overflow: hidden;

			@include mq(sp) {
				margin-right: 10%;
			}

			img {
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}

		&__content {
			margin-top: 35px;

			@include mq(sp) {
				width: 90%;
				margin: 35px auto 40px;
			}
		}
	}

	.p-page_service {
		position: relative;

		&__inner {
			margin: 0 auto;
			padding: 80px 0;

			@include mq(tablet, true) {
				width: $width_site;
				max-width: 1080px;
			}

			@include mq(tablet) {
				width: 90%;
				margin-top: -30px;
				padding: 40px 0;
			}
		}

		&__head {
			max-width: 670px;
		}

		&__title {
			.elem_en {
				@include rem(font-size, 7.0rem);
				font-family: $montserrat;
				font-weight: 700;
				color: #E6E6E6;
				line-height: 1;
				margin-bottom: 30px;

				@include mq(sp) {
					@include rem(font-size, 5.0rem);
				}
			}
		}

		&__list {
			li {
				background-color: #fff;
				margin-bottom: 10px;
				padding: 10px;
				counter-increment:decimal;

				span {
					display: block;
					margin-left:1.2em;
					text-indent:-1.2em;
					&::before{
						content: counter(decimal);
						background-color: $color_karitai;
						color: #fff;
						width: 27px;
						height: 27px;
						line-height: 27px;
						text-align: center;
						display: inline-block;
						border-radius: 50%;
						text-indent: 0;
						font-family: $montserrat;
						margin-right: 10px;
					}
					& > *{
						text-indent:0;
					}
				}

				&:last-of-type {
					margin-bottom: 0;
				}
			}
		}

		.notes {
			@include rem(font-size, 1.4rem);
			margin-top: 20px;
		}

		&__image {
			@include mq(tablet, true) {
				width: 38%;
				max-width: 520px;
				position: absolute;
				right: 0;
				bottom: 0;
			}

			@include mq(tablet) {
				width: 70%;
				margin-right: 0;
				margin-left: auto;
			}

			@include mq(sp) {
				width: 90%;

			}
		}
	}

	.p-page_payment {
		&__inner {
			display: flex;

			@include mq(tablet, true) {
				justify-content: space-between;
				flex-direction: row-reverse;
			}

			@include mq(tablet) {
				flex-direction: column-reverse;
			}
		}

		&__head {
			@include mq(sp, true) {
				max-width: 670px;
			}
		}

		&__title {
			.elem_en {
				@include rem(font-size, 7.0rem);
				font-family: $montserrat;
				font-weight: 700;
				color: #E6E6E6;
				line-height: 1;
				margin-bottom: 30px;

				@include mq(sp) {
					@include rem(font-size, 5.0rem);
					margin-bottom: 0px;
				}
			}

			.elem_jp {
				@include rem(font-size, 2.8rem);
				font-weight: 600;

				@include mq(sp) {
					@include rem(font-size, 2.4rem);
				}
			}
		}

		&__list {
			@include mq(sp) {
				margin-top: 30px;
			}

			> li {
				background-color: #F5F5F5;
				margin-bottom: 10px;
				counter-increment:decimal;

				@include mq(sp) {
					clear: both;
				}

				&::before{
					content:counter(decimal);
					background-color: $color_karitai;
					color: #fff;
					width: 47px;
					height: 47px;
					line-height: 47px;
					text-align: center;
					display: inline-block;
					text-indent: 0;
					font-family: $montserrat;
					margin-right: 15px;

					@include mq(sp) {
						float: left;
					}
				}
				> span {
					padding: 10px;
					padding-left: 0;
					@include rem(font-size, 2.2rem);
					line-height: 1.2;

					@include mq(sp) {
						@include rem(font-size, 2.0rem);
						overflow: hidden;
						display: block;
						padding: 12px;
					}
				}

				&:last-of-type {
					margin-bottom: 0;
				}

				> dl {
					padding: 30px;
					padding-top: 10px;

					@include mq(sp) {
						padding: 20px;
						padding-top: 10px;
					}

					> dt {
						@include rem(font-size, 1.8rem);
						margin-bottom: 10px;
						margin-top: 15px;

						@include mq(sp) {
							@include rem(font-size, 1.6rem);
						}

						&::before {
							content: "";
							display: inline-block;
							width: 12px;
							height: 12px;
							border-radius: 50%;
							background-color: $color_karitai;
							margin-right: 15px;
						}
					}

					> dd {
						padding: 0 25px;
						border-bottom: 1px solid #E6E6E6;
						padding-bottom: 15px;

						@include mq(sp) {
							padding: 0;
						}

						&:last-of-type {
							border-bottom: none;
						}

						ul {
							margin-top: 15px;
						}

						li {
							background-color: #fff;
							margin-bottom: 10px;
							padding: 15px 20px;

							&:last-of-type {
								margin-bottom: 0;
							}

							dt,dd {
								@include rem(font-size, 1.5rem);
							}

							dt {
								margin-bottom: 5px;
							}

							dd {
								letter-spacing: 0;
								font-weight: 500;

								span {
									color: #959595;
									margin-right: 20px;
								}
							}
						}
					}
				}
			}
		}

		&__content {
			@include mq(tablet, true) {
				width: 62%;
				margin-top: -35px;
			}

			@include mq(tablet) {
				margin-top: 0;
			}
		}

		&__image {
			@include mq(tablet, true) {
				width: 32%;
				margin-top: 70px;
			}

			@include mq(tablet) {
				width: 50%;
				margin: 20px auto 30px;
			}

			@include mq(sp) {
				width: 90%;
			}
		}
	}

}
