.c-voice {
	position: relative;
	background-color: #000;
	border-radius: 20px;

	@include mq(tablet, true) {
		width: 32%;
	}

	@include mq(tablet) {
		margin-top: 50px;
		width: 48%;
	}

	@include mq(sp) {
		width: 100%;
	}

	&::before {
		content: "";
		display: block;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 24px 24px 0 0;
		border-color: #000 transparent transparent transparent;
		position: absolute;
		left: 40px;
		bottom: -24px;
	}

	&.karitai {
		background-color: $color_karitai;
		.category {
			background-color: $color_karitai;
		}
		.meta {
			&__photo {
				border-color: $color_karitai;
			}
		}
		&::before {
			border-color: $color_karitai transparent transparent transparent;
		}
	}
	&.kaitai {
		background-color: $color_kaitai;
		.category {
			background-color: $color_kaitai;
		}
		.meta {
			&__photo {
				border-color: $color_kaitai;
			}
		}
		&::before {
			border-color: $color_kaitai transparent transparent transparent;
		}
	}
	&.kashitai {
		background-color: $color_kashitai;
		.category {
			background-color: $color_kashitai;
		}
		.meta {
			&__photo {
				border-color: $color_kashitai;
			}
		}
		&::before {
			border-color: $color_kashitai transparent transparent transparent;
		}
	}
	&.uritai {
		background-color: $color_uritai;
		.category {
			background-color: $color_uritai;
		}
		.meta {
			&__photo {
				border-color: $color_uritai;
			}
		}
		&::before {
			border-color: $color_uritai transparent transparent transparent;
		}
	}
	&.reform {
		background-color: $color_reform;
		.category {
			background-color: $color_reform;
		}
		.meta {
			&__photo {
				border-color: $color_reform;
			}
		}
		&::before {
			border-color: $color_reform transparent transparent transparent;
		}
	}

	a {
		position: relative;
		background-color: #F5F5F5;
		border-radius: 20px;
		display: block;
		transition: left .2s, top .2s;
		left: 0;
		top: 0;
		padding: 20px 30px;

		@include mq(liquid) {
			padding: 10px 20px 20px;
		}

		@include mq(tablet) {
			padding: 15px 15px;
		}

		@include mq(sp) {
			padding: 20px 25px 20px;
		}

		&::before {
			content: "";
			display: block;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 24px 24px 0 0;
			border-color: #F5F5F5 transparent transparent transparent;
			position: absolute;
			left: 40px;
			bottom: -24px;
		}
		&:hover {
			left: -10px;
			top: -10px;
			text-decoration: none;
		}
	}

	.category {
		@include rem(font-size, 1.8rem);
		color: #fff;
		background-color: $color_main;
		text-align: center;
		padding: 3px 30px;
		display: inline-block;
		border-radius: 100px;
		position: relative;
		top: -35px;
		min-width: 150px;
	}

	.comment {
		position: relative;
		padding-left: 35px;
		border-bottom: 2px solid $color_line;
		padding-bottom: 15px;

		@include mq(tablet) {
			margin-top: -15px;
		}

		&::before {
			content: "“";
			color: #ccc;
			@include rem(font-size, 4.8rem);
			position: absolute;
			left: 0px;
			line-height: 1;
		}

		&__title {
			@include rem(font-size, 2.0rem);
			line-height: 1.5;
			letter-spacing: 2px;
			font-weight: 600;

			@include mq(tablet) {
				@include rem(font-size, 1.8rem);
			}
		}

		&__name {
			@include rem(font-size, 1.5rem);
			color: #959595;
			margin-top: 10px;
		}
	}

	.meta {
		display: flex;
		justify-content: space-between;
		margin-top: 20px;

		&__photo {
			width: 110px;
			height: 110px;
			border: $color_main 4px solid;
			border-radius: 50%;
			overflow: hidden;
			position: absolute;
			right: -5px;
			bottom: -5px;
			transition: width .2s, height .2s;

			@include mq(liquid) {
				width: 90px;
				height: 90px;
			}
		}

		&__employee {
			display: flex;
			align-items: center;

			@include mq(liquid) {
				display: block;
			}

			@include mq(tablet) {
				display: flex;
			}

			dt {
				color: #959595;
				@include rem(font-size, 1.4rem);
				border: #959595 2px solid;
				line-height: 1;
				padding: 5px;
				margin-right: 20px;
				position: relative;
				border-radius: 0 4px 4px 0;
				z-index: 2;

				&::before,
				&::after {
					content: "";
					display: block;
					position: absolute;
					z-index: 1;
				}

				&::before {
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 14.5px 0 14.5px 12px;
					border-color: transparent transparent transparent #959595;
					right: -12px;
					top: -2px;
				}

				&::after {
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 12.5px 0 12.5px 10.5px;
					border-color: transparent transparent transparent #F5F5F5;
					right: -10px;
					top: 0px;
				}
			}

			dd {
				@include rem(font-size, 1.8rem);
			}
		}
	}
}
