.c-module_new_arrival {
	position: relative;

	@include mq(tablet) {
		width: 90%;
		margin: 0 auto;
	}

	&::before {
		@include mq(tablet, true) {
			content: "";
			display: block;
			width: 50%;
			height: calc(100% - 12px);
			background-color: #F1F1F1;
			border-top: $color_main 6px solid;
			border-bottom: $color_main 6px solid;
			border-right: none;
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			z-index: 1;
			vertical-align: top;
		}
	}
	&__inner {
		position: relative;
		z-index: 2;
		background-color: #F1F1F1;
		border: $color_main 6px solid;

		@include mq(tablet,true) {
			width: $width_site_wide;
			max-width: 1200px;
			margin: 0 auto;
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 55px 60px 55px 0;
			border-right: none;
			border-radius: 20px 0 0 20px;
		}

		@include mq(tablet) {
			border-width: 3px;
			border-radius: 20px;
			padding-top: 30px;
		}
	}

	&__head {
		@include mq(tablet, true) {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 23%;
		}

		@include mq(tablet) {
			position: relative;
			text-align: center;
		}

		.heading {
			background-color: $color_main;
			color: #fff;
			margin-bottom: 20px;

			@include mq(tablet, true) {
				padding: 20px 0 20px 60px;
				position: relative;
			}

			@include mq(tablet) {
				padding: 10px 0;
			}

			.elem_en {
				@include rem(font-size, 3.2rem);
				font-family: $montserrat;
				letter-spacing: 0px;

				@include mq(sp) {
					@include rem(font-size, 2.4rem);
					line-height: 1.5;
				}
			}

			.elem_jp {
				@include rem(font-size, 1.8rem);

				@include mq(sp) {
					@include rem(font-size, 1.4rem);
				}
			}

			&::before {
				@include mq(tablet, true) {
					content: "";
					display: block;
					background-image: url(../img/common/icon_new_arrival_01.png);
					background-size: cover;
					width: 30px;
					height: 25px;
					position: absolute;
					bottom: -25px;
					right: 30%;
				}
			}
		}

		.person {
			@include mq(tablet, true) {
				width: 74%;
				margin-left: 60px;
			}

			@include mq(tablet) {
				width: 22%;
				position: absolute;
				bottom: 0;
				left: 15px;
			}
		}
	}

	&__body {

		@include mq(sp, true) {
			width: 70%;
			display: flex;
			justify-content: space-between;
			margin-right: 0;
			margin-left: auto;
		}

		@include mq(tablet) {
			width: 100%;
			padding: 0 20px 55px;
		}

		.post {
			position: relative;

			@include mq(sp, true) {
				width: 32%;
			}

			@include mq(sp) {
				margin-top: 20px;
			}

			&:first-of-type{
				@include mq(sp) {
					margin-top: 0;
				}
			}

			&::before {
				content: "";
				display: block;
				width: 100%;
				height: 100%;
				background-color: $color_main;
				position: absolute;
				left: 0;
				top: 0;
				z-index: 1;
				border-radius: 20px;
			}

			a {
				display: block;
				transition: transform .3s;
				position: relative;
				z-index: 2;
				overflow: hidden;
				border-radius: 20px;
				background-color: #fff;
				&:hover {
					text-decoration: none;
					transform: translate(-10px,-10px);
				}
			}

			.content {
				padding: 15px 20px;

				.title {
					@include rem(font-size, 1.5rem);
					line-height: 1.5;
				}

				.price {
					color: #DE541E;
					@include rem(font-size, 1.8rem);
					font-family: $montserrat;
				}

				.address {
					@include rem(font-size, 1.3rem);
					border-top: 1px solid $color_line;
					margin-top: 10px;
					padding-top: 10px;
				}

				.spec {
					@include rem(font-size, 1.3rem);
					color: #959595;
				}
			}
		}
	}

	.p-btn {
		position: absolute;
		right: 60px;
		bottom: -35px;

		@include mq(sp) {
			right: 50%;
			transform: translateX(50%);
		}
	}

}