.c-cta {
	position: fixed;
	z-index: 10;

	@include mq(sp, true) {
		transform: translateX(60px) translateY(-50%);
		transition: transform .2s;
		right: 0;
		top: 50%;
	}

	@include mq(sp) {
		bottom: 0;
		left: 0;
		width: 100%;
		background-color: rgba(#fff, .8);
		padding: 10px 0;
	}

	&.is_active {
		@include mq(sp, true) {
			transform: translateX(0) translateY(-50%);
		}
	}

	&__list {
		@include mq(sp) {
			display: flex;
			justify-content: center;
		}
	}

	&__item {
		text-align: center;
		position: relative;

		@include mq(sp, true) {
			-ms-writing-mode: tb-rl;
			writing-mode: vertical-rl;
			min-height: 160px;
			margin-bottom: 10px;
			right: -15px;
			transition: right .2s;
		}
		@include mq(sp) {
			width: 43%;
			margin: 0 5px;
		}

		&:hover {
			right: 0;
		}

		a {
			display: block;
			letter-spacing: 2px;

			@include mq(sp, true) {
				@include rem(font-size, 1.4rem);
				padding: 25px 30px 25px 15px;
				border-radius: 10px 0 0 10px;
			}

			@include mq(sp) {
				border-radius: 10px;
				padding: 5px 0;
				@include rem(font-size, 1.3rem);
			}

			&:hover {
				text-decoration: none;
			}
		}

		&.info {
			a {
				background-color: $color_main;
				color: #fff;

				i {
					@include mq(sp, true) {
						margin-top: 10px;
					}

					@include mq(sp) {
						margin-left: 10px;
					}
				}
			}
		}

		&.request {
			a {
				background-color: #F7E733;
			}
		}

		&.contact {
			a {
				background-color: $color_main;
				color: #fff;

				i {
					margin-bottom: 10px;
				}
			}
		}
	}
}