/*

uritai page

*/

body.uritai{

	.l-page_hero {
		&::before {
			background-image: url(../img/uritai/hero_01.jpg);
		}
	}

	.p-page_method {
		&__list {
			@include mq(sp, true) {
				display: flex;
				flex-wrap: wrap;
			}
		}

		&__item {
			@include mq(sp, true) {
				width: 48%;
				margin-right: 4%;
				margin-bottom: 4%;
				min-height: 490px;
				position: relative;
			}

			@include mq(sp) {
				margin-top: 30px;
			}

			&:nth-child(2n) {
				margin-right: 0;
			}

			&:not(:first-of-type) {
				background-color: #F5F5F5;
				border-radius: 20px;
				padding: 45px;
				counter-increment:decimal;

				@include mq(sp) {
					padding: 20px;
				}

				&::before {
					content: "0" counter(decimal);
					font-family: $montserrat;
					position: absolute;
					right: 20px;
					top: 15px;
					@include rem(font-size, 12.0rem);
					color: #fff;
					line-height: 1;

					@include mq(sp) {
						@include rem(font-size, 6.0rem);
					}
				}
			}

			.copy {
				margin-top: 30px;
			}

			.lead {
				color: #959595;
				font-weight: 700;
				font-family: $montserrat;

				i {
					color: $color_uritai;
					@include rem(font-size, 2.0rem);
					margin-right: 5px;
					vertical-align: middle;
				}
			}
		}
	}

}
