/*

post

*/

.p-post_detail {
    &__content {
        margin-top: 60px;
        font-family: 'Montserrat', YakuHanJP, "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Noto Sans JP", Meiryo, sans-serif;;

        @include mq(sp) {
            margin-top: 30px;
        }
        a {
            text-decoration: underline;

            &:hover {
                text-decoration: none;
            }
        }

        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            clear: both;
            line-height: 1.846153846;
            margin: 24px 0;
        }

        hr {
            background-color: #ccc;
            border: 0;
            height: 1px;
            margin: 40px 0;
        }

        p{
            line-height: 2.1;
            margin: 0 0 30px;

            @include mq(sp) {
                line-height: 1.9;
            }
        }
        ul,
        ol {
            margin: 0 0 30px;
            line-height: 1.9;
            padding: 0;
        }
        ul {
            list-style: disc outside;
        }
        ol {
            list-style: decimal outside;
        }
        ul ul,
        ol ol,
        ul ol,
        ol ul {
            margin-bottom: 0;
        }
        li {
            margin: 0 0 0 30px;
        }
        dl {
            margin: 0 30px;
        }
        dt {
            font-weight: bold;
            margin-bottom: 30px;
        }
        dd {
            line-height: 1.714285714;
            margin: 0 0 30px;
        }
        strong {
            font-weight: bold;
        }
        cite,
        em,
        i {
            font-style: italic;
        }
        cite {
            border: none;
        }

        blockquote {
            font-style: italic !important;
            font-weight: normal;
            margin: 0;
            padding: 25px 30px;
            background-color: #eee;
        }
        pre {
            border: 1px solid #ededed;
            color: #666;
            font-family: Consolas, Monaco, Lucida Console, monospace;
            @include rem(font-size, 1.4rem);
            line-height: 1.714285714;
            margin: 30px 0;
            overflow: auto;
            padding: 30px;
        }
        code {
            background-color: $color_main;
            color: #fff;
            display: block;
            padding: 25px 30px;
        }
        code,
        kbd,
        samp,
        var {
            font-family: Consolas, Monaco, Lucida Console, monospace;
            @include rem(font-size, 1.4rem);
            line-height: 2;
        }
        abbr,
        acronym,
        dfn {
            border-bottom: 1px dotted #666;
            cursor: help;
        }
        address {
            display: block;
            line-height: 1.9;
            margin: 0 0 30px;
        }

        .alignleft {
            display: inline;
            float: left;
            margin: 15px 30px 15px 0;
        }
        .alignright {
            display: inline;
            float: right;
            margin: 15px 0 15px 30px;
        }
        .aligncenter {
            clear: both;
            display: block;
            margin-top: 15px;
            margin-bottom: 15px;
            margin-left: auto;
            margin-right: auto;
        }

        table {
            border-bottom: 1px solid $color_line;
            border-collapse: collapse;
            border-spacing: 0;
            @include rem(font-size, 1.4rem);
            line-height: 2;
            margin: 0 0 24px;
            width: 100%;
            letter-spacing: 0;

            thead {
                background-color: #eee;
            }
        }
        tr th {
            border: 1px solid $color_line;
            padding: 8px 10px;
            text-align: left;
        }
        td {
            border: 1px solid $color_line;
            padding: 8px 10px;
            text-align: left;
        }

        img,
        .editor-attachment {
            border: 0;
            max-width: 100%;
            border-radius: 10px;
            overflow: hidden;
        }
        img.size-full {
            width: auto/9; /* Prevent stretching of full-size images in IE8 */
        }
        img[class*="wp-image-"] {
            height: auto;
            max-width: 100%;
        }
        img[class*="align"],
        img[class*="wp-image-"],
        img[class*="attachment-"] {
            height: auto; /* Make sure images with WordPress-added height and width attributes are scaled correctly */
        }
        img.mce-wp-nextpage {
            border-radius: 0;
            box-shadow: none;
        }
        img.wp-smiley {
            border: 0;
            border-radius: 0;
            box-shadow: none;
            margin-bottom: 0;
            margin-top: 0;
            padding: 0;
        }
        .wp-caption {
            background: transparent;
            border: none;
            margin: 0;
            padding: 4px;
            text-align: left;
        }
        .wp-caption-dt {
            margin: 0;
        }
        .wp-caption .wp-caption-text,
        .wp-caption-dd {
            color: #757575;
            font-style: italic;
            line-height: 2;
            margin: 10px 0 30px;
            @include rem(font-size, 1.2rem);
        }
    }

}