/*

news page

*/

body.news{

	.p-page_news {
		&__list {
			@include mq(sp, true) {
				margin-top: -25px;
				margin-bottom: 50px;
			}
		}

		&__item {
			border-bottom: 1px solid $color_line;

			@include mq(sp) {
				margin-bottom: 30px;
				padding-bottom: 30px;
			}

			a {
				width: 100%;
				padding: 25px 0;

				@include mq(sp, true) {
					display: flex;
					justify-content: space-between;
				}

				&:hover {
					text-decoration: none;
					.title {
						color: $color_main;
					}
				}
			}

			.thumb {
				@include mq(sp, true) {
					width: 22%;
				}
				@include mq(sp) {
					margin-bottom: 20px;
				}
				img {
					border-radius: 10px;
					overflow: hidden;
				}
			}

			.content {
				@include mq(sp, true) {
					width: 74%;
				}
				position: relative;
			}

			.meta {
				display: flex;
				margin-bottom: 10px;
			}

			.date {
				@include rem(font-size, 1.4rem);
				font-family: $montserrat;
				color: #959595;
				margin-right: 20px;
			}

			.category {
				background-color: $color_main;
				color: #fff;
				@include rem(font-size, 1.2rem);
				border-radius: 100px;
				padding: 2px 10px;
			}

			.title {
				@include rem(font-size, 1.8rem);
				font-weight: 600;
				line-height: 1.5;
				transition: color .3s;
				margin-bottom: 10px;
			}

		}

	}

	.p-post_detail {
		&__head {
			border-bottom: 1px solid $color_line;
			padding-bottom: 40px;
			margin-bottom: 40px;

			@include mq(sp) {
				padding-bottom: 20px;
				margin-bottom: 20px;
			}

			.category {
				margin-bottom: 20px;
				a {
					background-color: $color_main;
					color: #fff;
					text-align: center;
					@include rem(font-size, 1.4rem);
					padding: 2px 30px;
					border-radius: 100px;
					display: inline-block;
					border: $color_main 2px solid;
					transition: color .2s, background-color .2s;
					min-width: 120px;

					@include mq(sp) {
						padding: 0 10px;
					}

					&:hover {
						text-decoration: none;
					}
				}

			}

			.title {
				@include rem(font-size, 3.6rem);
				margin-bottom: 20px;
				font-weight: 600;
				line-height: 1.5;

				@include mq(sp) {
					@include rem(font-size, 2.0rem);
				}

				span {
					display: block;
					overflow: hidden;
				}
			}

			.meta {
				.date {
					@include rem(font-size, 1.4rem);
					font-family: $montserrat;
					color: #959595;
				}
			}
		}

		&__content {

			h2 {
				@include rem(font-size, 2.8rem);
				line-height: 2.2;
				position: relative;
				padding: 10px 20px;
				background-color: #F5F5F5;
				border-left: 4px solid $color_reform;

				@include mq(sp) {
					@include rem(font-size, 2.0rem);
				}
			}
			h3 {
				@include rem(font-size, 2.2rem);
				line-height: 2.0;
				border-bottom: 2px solid $color_reform;

				@include mq(sp) {
					@include rem(font-size, 1.8rem);
				}
			}
			h4 {
				@include rem(font-size, 1.8rem);
				line-height: 1.8;
				@include mq(sp) {
					@include rem(font-size, 1.6rem);
				}
				&::before {
					content: "-";
					display: inline-block;
					vertical-align: middle;
					margin-right: 10px;
				}
			}
			h5 {
				@include rem(font-size, 1.6rem);
				line-height: 1.6;
			}
			h6 {
				@include rem(font-size, 1.4rem);
				line-height: 1.4;
			}
		}
	}

}
