.c-module_search_rent {

	&__inner {
		z-index: 2;
		background-color: #EFF7FF;
		border-right: none;
		overflow: hidden;
		position: relative;

		@include mq(tablet,true) {
			width: $width_site_wide;
			max-width: 1200px;
			margin: 0 auto;
			padding: 55px 60px;
			border-radius: 20px;
		}

		@include mq(tablet) {
			padding: 40px 20px;
		}
	}

	&__wrap {
		@include mq(tablet,true) {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

	&__head {
		@include mq(tablet,true) {
			width: 23%;
		}

		&.p-icon--search::before {
			position: absolute;
			left: 20px;
			top: 20px;
			@include rem(font-size, 5.2rem);
			color: #fff;
			z-index: 2;

			@include mq(tablet) {
				left: 10px;
				top: 10px;
				@include rem(font-size, 4.6rem);
			}
		}

		&::after {
			content: "";
			display: block;
			position: absolute;
			left: -115px;
			top: -115px;
			display: block;
			width: 230px;
			height: 230px;
			border-radius: 50%;
			background-color: $color_main;
			z-index: 1;

			@include mq(sp) {
				width: 180px;
				height: 180px;
				left: -90px;
				top: -90px;
			}
		}

		.heading {
			color: $color_main;

			@include mq(tablet) {
				text-align: center;
				margin-bottom: 30px;
			}

			.elem_en {
				@include rem(font-size, 3.2rem);
				font-family: $montserrat;
				letter-spacing: 0px;
				margin-bottom: 20px;

				@include mq(tablet) {
					@include rem(font-size, 2.4rem);
					margin-bottom: 10px;
				}
			}
			.elem_jp {
				@include rem(font-size, 1.8rem);

				@include mq(tablet) {
					@include rem(font-size, 1.4rem);
				}
			}
		}

		.person {
			width: 74%;
			margin-left: 60px;

			@include mq(tablet) {
				display: none;
			}
		}
	}

	&__body {
		@include mq(tablet, true) {
			width: 70%;
		}
	}

}