.p-border_line_b {

	&--pt01 {
		position: relative;
		&::before {
			content: "";
			display: block;
			width: 70px;
			height: 2px;
			background-color: $color_main;
			position: absolute;
			left: 50%;
			bottom: -10px;
			transform: translateX(-50%);
		}
	}

	&--pt02 {
		position: relative;
		&::before {
			content: "";
			display: block;
			width: 20px;
			height: 5px;
			background-color: $color_main;
			position: absolute;
			left: 0;
			bottom: -10px;
		}
	}

}

.p-text_black {
	color: $color_main;
}