/*

privacy page

*/

body.privacy{

	.p-page_privacy {
		@include mq(sp, true) {
			width: 68%;
			margin: 0 auto;
		}

		.p-heading_lv3 {
			margin-top: 8%;
		}

		.attention {
			color: #FF6666;
			border: #FF6666 1px solid;
			padding: 20px;
			margin: 4% 0;
		}

		> dl,
		> ul {
			margin-top: 3%;

			a {
				text-decoration: underline;
				&:hover {
					text-decoration: none;
				}
			}
		}

		.notes {
			@include rem(font-size, 1.4rem);
		}
	}

}
