.c-widget {
	+ .c-widget {
		margin-top: 40px;
	}

	&__title {
		@include rem(font-size, 1.8rem);
		font-weight: 700;
		margin-bottom: 10px;
		font-family: $montserrat;
		position: relative;
		padding-top: 5px;

		&::before,
		&::after {
			content: "";
			display: block;
			position: absolute;
			height: 2px;
			left: 0;
			top: 0;
		}

		&::before {
			width: 30px;
			background-color: $color_main;
			z-index: 2;
		}

		&::after {
			width: 100%;
			background-color: #E6E6E6;
			z-index: 1;
		}
	}

	.category_list {
		a {
			display: block;
			padding: 5px 0;
			transition: padding .2s;
			@include rem(font-size, 1.4rem);

			&::before {
				content: "-";
				display: inline-block;
				margin-right: 5px;
			}


			&:hover {
				padding-left: .5em;
				text-decoration: none;
			}
		}
	}

	.archive_list {
		> li {
			> a {
				display: block;
				padding: 5px 0;
				position: relative;
				transition: padding .2s;
				@include rem(font-size, 1.4rem);

				&:hover {
					padding-left: .5em;
					text-decoration: none;
				}
			}
		}
	}
}