/*

promise page

*/

body.promise{
	.p-page_promise {

		&__inner {
			@include mq(sp, true) {
				padding-left: 30px;
			}
		}

		.knowledge {
			li {
				margin-bottom: 10px;
				width: 100%;
				align-items: center;
				background-color: #F5F5F5;

				@include mq(sp, true) {
					display: flex;
				}

				@include mq(sp) {
				}

				&:last-child {
					margin-bottom: 0;
				}

				b {
					background-color: $color_main;
					color: #fff;
					text-align: center;
					padding: 15px 0;
					@include rem(font-size, 1.8rem);

					@include mq(sp, true) {
						width: 120px;
					}

					@include mq(sp) {
						@include rem(font-size, 1.6rem);
						padding: 7px 0;
						display: block;
					}
				}

				span {
					display: inline-block;
					padding-left: 20px;
					width: calc(100% - 20px);

					@include mq(sp) {
						padding: 10px 20px;
					}
				}
			}
		}

		.mission {
			li {
				display: flex;
				margin-bottom: 10px;
				width: 100%;
				align-items: center;
				background-color: #fff;

				&:last-child {
					margin-bottom: 0;
				}
				b {
					background-color: $color_main;
					color: #fff;
					text-align: center;
					width: 50px;
					padding: 10px 0;
				}

				span {
					display: inline-block;
					padding-left: 20px;
					width: calc(100% - 50px);

					@include mq(sp) {
						padding: 10px;
					}
				}
			}
		}

	}
}
