/*

survey page

*/

body.survey{
	.l-page_section {
		@include mq(sp) {
			margin-bottom: 100px;
		}
	}
	.p-page_survey {

		&__head {

		}

		&__title {
			@include rem(font-size, 2.8rem);
			margin-bottom: 25px;

			@include mq(sp) {
				@include rem(font-size, 2.2rem);
				margin-bottom: 15px;
			}

			+ p {
				line-height: 2;

				@include mq(sp) {
					line-height: 1.8;
				}
			}
		}

		&__body {
			position: relative;

			@include mq(sp, true) {
				margin-top: 100px;
				padding: 80px 0 100px;
			}

			@include mq(sp) {
				margin-top: 30px;
			}
		}

		&__inner {
			background-color: #fff;
			border-radius: 20px;
			margin-top: 40px;
			padding: 5% 5% 7% 5%;

			.p-heading_lv3 {
				margin-top: 5%;

				@include mq(sp) {
					margin-top: 30px;
				}

				&:first-of-type {
					margin-top: 0;
				}
			}
		}

	}
}
