.l-column_container{
	display: flex;
	justify-content: space-between;
	align-items: flex-start;

	@include mq(tablet) {
		flex-direction: column;
	}
}

.l-primary_column{
	width: $width_main_column;

	@include mq(tablet) {
		width: 100%;
	}

	&__inner {
		display: flex;
		flex-wrap: wrap;
	}
}

.l-secondary_column{
	width: $width_sub_column;

	@include mq(tablet) {
		width: 90%;
		margin: 60px auto 0;
	}

}
