/*

voice page

*/

body.voice{

	.p-page_voice {
		&__list {
			display: flex;
			flex-wrap: wrap;
		}

		&__head {
			@include mq(tablet, true) {
				margin-bottom: 50px;
			}
			.navi {
				display: flex;
				justify-content: space-between;

				@include mq(tablet, true) {
					padding: 0 20px;
					border-bottom: 2px solid $color_main;
				}

				@include mq(tablet) {
					flex-wrap: wrap;
				}

				&__item {
					width: 16%;

					@include mq(tablet) {
						width: 48%;
						margin-bottom: 15px;
					}

					a {
						display: block;
						text-align: center;
						padding: 12px 10px;
						border: $color_main 2px solid;
						position: relative;
						transition: color .2s, background-color .2s;

						@include mq(tablet, true) {
							border-radius: 10px 10px 0 0;
							border-bottom: none;
						}

						@include mq(tablet) {
							border-radius: 10px;
						}

						&:hover {
							text-decoration: none;
						}
					}

					&.all {
						a {
							color: $color_main;

							&:hover {
								background-color: $color_main;
								color: #fff;
							}
						}

						&.current {
							a {
								background-color: $color_main;
								color: #fff;
							}
						}
					}

					&.karitai {
						a {
							color: $color_karitai;
							border-color: $color_karitai;

							&:hover {
								background-color: $color_karitai;
								color: #fff;
							}
						}

						&.current {
							a {
								background-color: $color_karitai;
								color: #fff;
							}
						}
					}

					&.kashitai {
						a {
							color: $color_kashitai;
							border-color: $color_kashitai;

							&:hover {
								background-color: $color_kashitai;
								color: #fff;
							}
						}

						&.current {
							a {
								background-color: $color_kashitai;
								color: #fff;
							}
						}
					}

					&.uritai {
						a {
							color: $color_uritai;
							border-color: $color_uritai;

							&:hover {
								background-color: $color_uritai;
								color: #fff;
							}
						}

						&.current {
							a {
								background-color: $color_uritai;
								color: #fff;
							}
						}
					}

					&.kaitai {
						a {
							color: $color_kaitai;
							border-color: $color_kaitai;

							&:hover {
								background-color: $color_kaitai;
								color: #fff;
							}
						}

						&.current {
							a {
								background-color: $color_kaitai;
								color: #fff;
							}
						}
					}

					&.reform {
						a {
							color: $color_reform;
							border-color: $color_reform;

							&:hover {
								background-color: $color_reform;
								color: #fff;
							}
						}

						&.current {
							a {
								background-color: $color_reform;
								color: #fff;
							}
						}
					}

				}
			}
		}

		.p-module_voice__item {
			margin-right: 2%;
			margin-bottom: 6%;

			@include mq(tablet) {
				margin-right: 4%;
			}

			@include mq(sp) {
				margin-right: 2%;
			}

			&:nth-child(2n) {
				@include mq(tablet) {
					margin-right: 0;
				}
			}

			&:nth-child(3n) {
				@include mq(tablet, true) {
					margin-right: 0;
				}
			}
		}
	}

	.p-post_detail {
		&__head {
			margin-bottom: 50px;

			@include mq(tablet) {
				margin-bottom: 30px;
			}

			.category {
				margin-bottom: 20px;
				a {
					background-color: $color_main;
					color: #fff;
					text-align: center;
					@include rem(font-size, 1.4rem);
					padding: 2px 30px;
					border-radius: 100px;
					display: inline-block;
					border: $color_main 2px solid;
					transition: color .2s, background-color .2s;
					min-width: 120px;

					@include mq(sp) {
						padding: 0 10px;
					}

					&:hover {
						text-decoration: none;
					}
				}

				&.karitai {
					a {
						background-color: $color_karitai;
					}
				}

				&.kashitai {
					a {
						background-color: $color_kashitai;
					}
				}

				&.uritai {
					a {
						background-color: $color_uritai;
					}
				}

				&.kaitai {
					a {
						background-color: $color_kaitai;
					}
				}

				&.reform {
					a {
						background-color: $color_reform;
					}
				}
			}

			.title {
				@include rem(font-size, 3.6rem);
				margin-bottom: 20px;
				font-weight: 600;
				line-height: 1.5;
				&::before {
					content: "“";
					@include rem(font-size, 4.8rem);
					color: #ccc;
					float: left;
					margin-right: 15px;
					font-weight: 600;
				}

				@include mq(sp) {
					@include rem(font-size, 2.4rem);
					line-height: 1.7;
					margin-bottom: 15px;
				}

				span {
					display: block;
					overflow: hidden;
				}
			}

			.meta {
				padding-left: 45px;
				.name {
					color: #959595;
					@include rem(font-size, 1.5rem);
				}
			}

			.profile {
				margin-top: 30px;
				background-color: #F5F5F5;
				border-radius: 10px;
				position: relative;

				@include mq(sp, true) {
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
					align-items: center;
					padding: 30px 170px 30px 30px;
				}

				@include mq(sp) {
					padding: 20px;
				}

				&__name{
					display: flex;
					align-items: center;
					dt {
						color: #959595;
						@include rem(font-size, 1.4rem);
						border: #959595 2px solid;
						line-height: 1;
						padding: 5px;
						margin-right: 20px;
						position: relative;
						border-radius: 0 4px 4px 0;
						z-index: 2;
						display: inline-block;

						&::before,
						&::after {
							content: "";
							display: block;
							position: absolute;
							z-index: 1;
						}

						&::before {
							width: 0;
							height: 0;
							border-style: solid;
							border-width: 14.5px 0 14.5px 12px;
							border-color: transparent transparent transparent #959595;
							right: -12px;
							top: -2px;
						}

						&::after {
							width: 0;
							height: 0;
							border-style: solid;
							border-width: 12.5px 0 12.5px 10.5px;
							border-color: transparent transparent transparent #F5F5F5;
							right: -10px;
							top: 0px;
						}
					}

					dd {
						@include rem(font-size, 1.8rem);
					}
				}

				&__photo {
					span {
						display: block;
						width: 140px;
						height: 140px;
						border: $color_main 4px solid;
						border-radius: 70px;
						overflow: hidden;
						position: absolute;
						right: 10px;
						top: -10px;

						@include mq(sp, true) {

						}

						@include mq(sp) {
							width: 80px;
							height: 80px;
						}
					}

					figcaption {
						color: #959595;
						@include rem(font-size, 1.5rem);
					}
				}

				&__text {
					width: 100%;
					border-top: 1px solid #E6E6E6;
					padding-top: 10px;
					margin-top: 10px;

					@include mq(sp, true) {
						margin-left: 94px;
					}
				}
			}
		}

		&__content {
			border-bottom: 1px solid #E6E6E6;
			padding-bottom: 80px;

			@include mq(sp) {
				padding-bottom: 40px;
			}

			h2 {
				@include rem(font-size, 2.8rem);
				line-height: 2.2;
				position: relative;
				padding: 20px 0;

				@include mq(sp) {
					@include rem(font-size, 2.0rem);
				}

				&::before,
				&::after {
					content: "";
					display: block;
					position: absolute;
					top: 0;
					left: 0;
					height: 4px;
				}

				&::before {
					width: 80px;
					background-color: $color_main;
					z-index: 2;
				}

				&::after {
					width: 100%;
					background-color: #E6E6E6;
					z-index: 1;
				}
			}
			h3 {
				@include rem(font-size, 2.2rem);
				line-height: 2.0;

				@include mq(sp) {
					@include rem(font-size, 1.8rem);
				}
			}
			h4 {
				@include rem(font-size, 1.8rem);
				line-height: 1.8;

				@include mq(sp) {
					@include rem(font-size, 1.6rem);
				}
			}
			h5 {
				@include rem(font-size, 1.6rem);
				line-height: 1.6;
			}
			h6 {
				@include rem(font-size, 1.4rem);
				line-height: 1.4;
			}
		}

		&__episode {
			margin-top: 70px;

			@include mq(sp) {
				margin-top: 30px;
			}

			a {
				display: inline-block;
				position: relative;
				padding: 20px 0 20px 110px;
				transition: color .4s;

				@include mq(sp) {
					padding: 15px 0 15px 100px;
					display: block;
				}

				&::before {
					content: "";
					display: block;
					width: 100%;
					height: 100%;
					background-color: #F5F5F5;
					border-radius: 100px;
					position: absolute;
					left: 0;
					top: 0;
					transform: scale(1);
					z-index: 1;
					transition: transform .2s, background-color .2s;
					transform-origin: left center;
				}

				&:hover {
					text-decoration: none;
					color: #fff;
					&::before {
						background-color: $color_main;
						color: #fff;
						transform: scale(1.05);
					}
					i {
						color: #fff;
					}
				}

				i {
					transition: color .4s;
				}
			}

			.photo {
				position: absolute;
				top: 50%;
				left: 0;
				width: 90px;
				height: 90px;
				border-radius: 45px;
				z-index: 2;
				overflow: hidden;
				border: $color_main 4px solid;
				transform: translateY(-50%);

				@include mq(sp) {
					width: 60px;
					height: 60px;
					left: 20px;
				}
			}

			.title {
				@include rem(font-size, 1.5rem);
				position: relative;
				z-index: 2;

				@include mq(sp) {
					@include rem(font-size, 1.4rem);
				}
			}

			i {
				margin: 0 30px 0 20px;
				color: $color_main;

				@include mq(sp) {
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
				}
			}
		}
	}

}
