.p-module_message {

	&__head {
		width: $width_site;
		max-width: 1080px;
		margin: 0 auto;

		@include mq(sp) {
			width: 90%;
		}
	}

	&__body {
		margin-top: 40px;
	}

	&__wrap {
		padding: 40px 0 60px;
		position: relative;
		margin-bottom: 20px;
		counter-increment:decimal;

		@include mq(sp) {
			width: 90%;
			background-color: #F5F5F5;
			border-radius: 20px;
			padding: 20px;
			margin: 0 auto 20px;
		}

		&::before {
			content: "0" counter(decimal);
			font-family: $montserrat;
			color: #fff;
			line-height: 1;
			@include rem(font-size, 12.0rem);
			position: absolute;
			left: 0;
			top: 20px;
			z-index: 2;

			@include mq(sp) {
				@include rem(font-size, 6.0rem);
				left: 10px;
				top: 10px;
			}
		}

		&::after {
			@include mq(sp, true) {
				content: "";
				display: block;
				width: 84%;
				height: 100%;
				background-color: #F5F5F5;
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				z-index: 1;
				border-radius: 0 20px 20px 0;
				max-width: 1143px;
			}
		}
	}

	&__inner {
		position: relative;
		z-index: 2;
		margin: 0 auto;

		@include mq(sp, true) {
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: $width_site;
			max-width: 1080px;
		}

		&:last-of-type {
			@include mq(sp, true) {
				border-bottom: none;
				padding-bottom: 0;
			}
		}
	}

	&__column {

		&__inner {
			@include mq(sp, true) {
				display: flex;
				justify-content: space-between;
				align-items: center;
			}
		}

		&__content {
			@include mq(sp, true) {
				width: 55%;
			}

			.lead {
				color: #959595;
				font-weight: 700;
				font-family: $montserrat;

				i {
					color: $color_main;
					@include rem(font-size, 2.0rem);
					margin-right: 5px;
					vertical-align: middle;

					&.kaitai {
						color: $color_kaitai;
					}

					&.reform {
						color: $color_reform;
					}
				}
			}
		}

		&__image {
			@include mq(sp, true) {
				width: 37%;
			}
			@include mq(sp) {
				margin-top: 20px;
			}
			img {
				border-radius: 20px;
				overflow: hidden;
			}
		}
	}

}