/*

privilege page

*/

body.privilege{

	.p-page_privilege {

		&:last-of-type {
			margin-bottom: 7%;
		}

		&__top {
			&__inner {
				display: flex;
				justify-content: space-between;

				@include mq(sp) {
					flex-direction: column-reverse;
				}
			}

			&__head {
				@include mq(sp, true) {
					width: 62%;
					padding-left: 30px;
				}
			}

			&__image {
				@include mq(sp, true) {
					width: 28%;
				}

				@include mq(sp) {
					margin-bottom: 30px;
				}
			}
		}

		&__bottom {
			position: relative;

			@include mq(sp) {
				background-color: #f5f5f5;
			}

			&::before {
				@include mq(sp, true) {
					content: "";
					display: block;
					width: 94%;
					height: 100%;
					background-color: #F5F5F5;
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
					border-radius: 20px 0 0 20px;
				}
			}
			&__inner {
				@include mq(sp, true) {
					display: flex;
					justify-content: space-between;
				}
			}

			&__head {
				@include mq(sp, true) {
					width: 62%;
					padding-left: 30px;
				}
			}

			&__image {
				@include mq(sp, true) {
					width: 30%;
				}

				@include mq(sp) {
					margin-top: 30px;
				}
			}
		}
	}

}
