.p-table {
	&__form,
	&__survey,
	&__search {
		em {
			@include rem(font-size, 1.4rem);
			font-style: normal;
			color: #fff;
			background-color: #FF6666;
			padding: 8px 10px;
			line-height: 1;
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
		}

		.u-valign_t {
			em {
				transform: translateY(0);
				top: 25px;
			}
		}

		label {
			display: inline-block;
			cursor: pointer;
			transition: opacity .3s;

			@include mq(sp) {
				@include rem(font-size, 1.5rem);
			}

			&:hover {
				opacity: 0.6;
			}
		}
		input[type="text"],
		input[type="email"],
		input[type="date"],
		input[type="time"],
		input[type="number"],
		textarea {
			-moz-appearance: none;
			-webkit-appearance: none;
			@include rem(font-size, 1.6rem);
			border-radius: 10px;
			padding: 15px 20px;
			width: 100%;
			transition: border-color .6s;
			outline: none;
			box-sizing: border-box;
			border: none;
			vertical-align: middle;

			&:focus {
				border-color: $color_main;
			}

			&.size_s {
				width: 20%;
				min-width: 120px;
				margin-right: 10px;

				@include mq(sp) {
					min-width: 80px;
				}
			}

			&.size_s2 {
				width: 70px;
				margin-right: 5px;

				@include mq(sp) {
					min-width: 70px;
				}
			}

			&.customer_id {
				border: $color_line 1px solid;
			}
		}

		input[type="radio"],
		input[type="checkbox"] {
			transform: scale(1.5);
			margin-right: 10px;
		}

		input[type="checkbox"] {
			position: relative;
			top: -2px;
		}

		select {
			border: none;
			padding: 15px 20px;
			border-radius: 10px;
		}

		textarea {
			@include mq(sp, true) {
				min-width: 100%;
				max-width: 560px;
				min-height: 164px;
				max-height: 400px;

			}

			@include mq(sp) {
				min-height: 150px;
				margin-bottom: 0;
			}
		}

		.zip_01 {
			max-width: 80px;
			margin: 0 10px;

			@include mq(tablet) {
				margin: 0 3px;
				max-width: 70px;
			}
		}

		.zip_02 {
			max-width: 120px;
			margin: 0 10px;

			@include mq(tablet) {
				margin: 0 3px;
				max-width: 80px;
			}
		}

		.address {
			li {
				margin-bottom: 2%;

				&:last-of-type {
					margin-bottom: 0;
				}
			}
		}

		.age {
			display: flex;
			align-items: center;

			li {
				margin-right: 30px;

				&:last-child {
					margin-right: 0;
				}

				.mwform-radio-field {

					label {
						display: flex;
						align-items: center;
					}
				}

			}
		}

		.date,
		.time {
			li {
				margin-bottom: 10px;
			}

			input {
				@include mq(sp, true) {
					margin: 0 10px;
				}

				@include mq(sp) {
					margin-right: 10px;
					margin-bottom: 10px;
				}
			}

			input[type="date"] {
				@include mq(sp, true) {
					width: 50%;
				}
			}

			.date-text {
				@include mq(sp, true) {
					width: 50%;
				}
			}

			input[type="time"] {
				@include mq(sp, true) {
					width: 30%;
				}

				@include mq(sp) {
					width: 80%;
				}
			}

			.time-text {
				@include mq(sp, true) {
					width: 80px;
					margin-right: 5px;
				}

				@include mq(sp) {
					width: 80px;
					margin-right: 5px;
				}
			}

			.time02-text {
				@include mq(sp, true) {
					width: 80px;
					margin-right: 5px;
					margin-left: 5px;
				}

				@include mq(sp) {
					width: 80px;
					margin-right: 5px;
					margin-left: 5px;
				}
			}
		}
	}

	&__search {
		@include mq(sp, true) {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
		}

		dl {
			margin-top: 25px;
			display: flex;
			align-items: center;
			@include rem(font-size, 1.5rem);

			@include mq(sp, true) {
				width: 46%;
			}

			@include mq(sp) {
				align-items: flex-start;
			}

			dt {
				width: 110px;
			}

			dd {
				width: calc(100% - 110px);

				select {
					width: 100%;
				}
			}
		}

		.type {
			margin-top: 0;
			width: 100%;

			label {
				margin-right: 20px;
			}
		}


	}

}