/*

investors page

*/

body.investors{
	.p-page_promise {

		&__inner {
			padding-left: 30px;
		}

		.knowledge {
			li {
				display: flex;
				margin-bottom: 10px;
				width: 100%;
				align-items: center;
				background-color: #F5F5F5;

				&:last-child {
					margin-bottom: 0;
				}
				b {
					background-color: $color_main;
					color: #fff;
					text-align: center;
					padding: 15px 0;
					@include rem(font-size, 1.8rem);
					width: 120px;
				}

				span {
					display: inline-block;
					padding-left: 20px;
					width: calc(100% - 20px);
				}
			}
		}

		.mission {
			li {
				display: flex;
				margin-bottom: 10px;
				width: 100%;
				align-items: center;
				background-color: #fff;

				&:last-child {
					margin-bottom: 0;
				}
				b {
					background-color: $color_main;
					color: #fff;
					text-align: center;
					width: 50px;
					padding: 10px 0;
				}

				span {
					display: inline-block;
					padding-left: 20px;
					width: calc(100% - 50px);
				}
			}
		}

	}
}
