.l-home_content{
	position: relative;
	padding-top: 20px;
	padding-bottom: 60px;

	@include mq(sp) {
		padding-bottom: 0;
	}

	&::before {
		content: "";
		display: block;
		width: 100%;
		height: 100px;
		background-color: $color_main;
		position: absolute;
		left: 0;
		top: 0;
		z-index: -1;
	}

	&::after {
		@include mq(tablet, true) {
			content: "";
			display: block;
			width: 100%;
			height: 530px;
			background-color: #F1F1F1;
			position: absolute;
			bottom: 0;
			left: 0;
			z-index: 0;
		}
	}

	@include mq(sp) {
		overflow-x: hidden;
	}
}

.l-home_section{
	padding: 30px 0;

	@include mq(tablet) {
		padding: 50px 0;
	}

	@include mq(sp) {
		padding: 40px 0;
	}

	&__inner{
		margin:0 auto;
		position: relative;
		z-index: 1;

		@include mq(sp, true) {
			width: $width_site;
			max-width: 1080px;
		}

		@include mq(sp) {
			width: 84%;
		}

		@include mq(liquid) {
			width: 90%;
		}
	}
}

.l-page_content{
	border-top: 50px solid $color_main;
	//padding-bottom: 7%;

	@include mq(sp) {
		//padding-bottom: 90px;
		border-top-width: 40px;
	}
}

.l-page_hero {
	width: 100%;
	position: relative;
	display: flex;

	@include mq(sp) {
		flex-direction: column-reverse;
	}

	&__image {
		@include mq(sp, true) {
			width: 62%;
			margin: -30px 0 0 auto;
		}
	}

	&__inner {
		margin:0 auto;
		position: relative;
		padding: 5% 0;

		@include mq(sp,true) {
			width: $width_site;
			max-width: 1080px;
			min-height: 320px;
		}

		@include mq(tablet) {
			padding: 60px 0;
		}

		@include mq(sp) {
			padding: 40px 0;
		}
	}

	&::before {
		content: "";
		display: block;
		background-size: cover;
		background-position: center;
		width: 62%;
		height: 20vw;
		border-radius: 20px 0 0 20px;
		overflow: hidden;

		@include mq(sp, true) {
			position: absolute;
			right: 0;
			top: -30px;
			min-height: 370px;
			max-height: 370px;
			max-width: 1200px;
		}

		@include mq(tablet) {
			min-height: 300px;
			max-height: 300px;
		}

		@include mq(sp) {
			width: 94%;
			margin: 0 0 0 auto;
			min-height: 200px;
		}
	}
}

.l-page_head {
	background-color: #F7F7F7;
	position: relative;

	&__inner {
		margin:0 auto;
		position: relative;

		@include mq(sp,true) {
			padding: 7% 0 4%;
			width: $width_site;
			max-width: 1080px;
		}

		@include mq(sp) {
			padding: 40px 20px 10px;
		}

		&::after {
			content: "";
			display: block;
			width: 60px;
			height: 4px;
			background-color: $color_main;
			position: absolute;
			left: 0;
			bottom: 0;
		}

		&.reform {
			&::after {
				background-color: $color_reform;
			}
		}
	}
}

.l-page_section{
	padding: 80px 0;

	@include mq(liquid) {
		padding: 80px 0;
	}

	@include mq(tablet) {
		padding: 20px 0 100px;
	}

	@include mq(sp) {
		padding: 30px 0 100px;
	}

	&__inner{
		margin:0 auto;
		position: relative;

		@include mq(sp,true) {
			width: $width_site;
			max-width: 1080px;
		}

		@include mq(liquid) {
			width: 90%;
		}

		&.wide {
			@include mq(sp,true) {
				width: 88%;
				max-width: 1200px;
			}

			background-color: #F1F1F1;
			border-radius: 20px;
			padding: 6% 5%;
		}
	}

	&.bg--gray {
		background-color: #F5F5F5;
	}
}

.p-page_sub_section {

	+ .p-page_sub_section {
		margin-top: 60px;
	}

	&__inner {
		@include mq(sp, true) {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

	&__head {
		@include mq(sp, true) {
			width: 45%;
		}
	}

	&__body {
		@include mq(sp, true) {
			width: 52%;
		}

		@include mq(sp) {
			margin-top: 30px;
		}
	}

	&__title {
		@include rem(font-size, 4.0rem);
		line-height: 1.5;

		@include mq(tablet) {
			@include rem(font-size, 3.0rem);
		}

		@include mq(sp) {
			margin-bottom: 20px;
		}

		.elem_en {
			color: $color_main;
			font-family: $montserrat;
			@include rem(font-size, 1.8rem);
			font-weight: 700;
			position: relative;
			display: inline-block;

			@include mq(sp) {
				@include rem(font-size, 1.4rem);
			}

			&::after {
				content: "";
				display: block;
				background-color: $color_main;
				width: 80px;
				height: 2px;
				position: absolute;
				right: -95px;
				top: 50%;
				margin-top: -2px;
			}

			&.karitai {
				color: $color_karitai;
				&::after {
					background-color: $color_karitai;
				}
			}

			&.kashitai {
				color: $color_kashitai;
				&::after {
					background-color: $color_kashitai;
				}
			}

			&.kaitai {
				color: $color_kaitai;
				&::after {
					background-color: $color_kaitai;
				}
			}

			&.uritai {
				color: $color_uritai;
				&::after {
					background-color: $color_uritai;
				}
			}

			&.reform {
				color: $color_reform;
				&::after {
					background-color: $color_reform;
				}
			}
		}

		.elem_jp {
			> span {
				color: #fff;
				display: inline-block;
				line-height: 1.3;
				padding: 0 5px;
				position: relative;

				&::before {
					content: "";
					width: 100%;
					display: block;
					height: 100%;
					background-color: $color_main;
					position: absolute;
					left: 0;
					top: 0;
					z-index: 1;
					transform: scaleX(0);
					transition: transform .6s cubic-bezier(1.000,0.000,0.000,1.000);
					transform-origin: left center;
				}

				> b {
					position: relative;
					z-index: 2;
					font-weight: 600;
				}
			}

			&.karitai {
				> span {
					&::before {
						background-color: $color_karitai;
					}
				}
			}

			&.kashitai {
				> span {
					&::before {
						background-color: $color_kashitai;
					}
				}
			}

			&.kaitai {
				> span {
					&::before {
						background-color: $color_kaitai;
					}
				}
			}

			&.uritai {
				> span {
					&::before {
						background-color: $color_uritai;
					}
				}
			}

			&.reform {
				> span {
					&::before {
						background-color: $color_reform;
					}
				}
			}
		}

		&.is-animated {
			span {
				&::before {
					transform: scaleX(1);
				}
			}
		}
	}

	&__image {
		border-radius: 20px;
		overflow: hidden;
		max-width: 420px;
	}

}

.p-sub_section {

	@include mq(sp) {
		width: 86%;
		margin: 0 auto;
	}

	//2カラムパターン
	// reverseを付与すると写真とテキストが逆転
	&--col2 {

		@include mq(sp, true) {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}

		&.reverse {
			flex-direction: row-reverse;
		}

		.image {
			@include mq(sp, true) {
				width: 36%;
			}

			@include mq(sp) {
				width: 70%;
				margin: 0 auto 30px;
			}
		}

		.body {
			@include mq(sp, true) {
				width: 57%;
			}

			.p-heading_lv2 {
				@include mq(sp) {
					text-align: center;
				}
			}
		}
	}

	.notes {
		font-weight: 500;
		margin-top: 10px;
		@include rem(font-size, 1.2rem);
	}


}

