/*

3re page

*/

body.page_3re{

	.l-page_head {
		.p-heading_lv1 {
			font-family: $montserrat;
		}
	}

	.p-page_mv {
		background-image: url(../img/reform/3re/mainvisual_01.jpg);
		background-size: cover;
		background-position: center bottom;
	}

	.p-page_icons {
		background-color: rgba(#fff, .8);

		@include mq(liquid, true) {
			width: 67%;
			padding: 40px 60px;
			border-radius: 20px 0 0 20px;
			margin: 60px 0 0 auto;
		}

		@include mq(liquid) {
			width: 90%;
			margin: 20px auto 0;
			border-radius: 20px;
			padding: 40px;
		}

		@include mq(tablet) {
			padding: 20px;
		}

		ul {
			display: flex;
			justify-content: flex-start;

			@include mq(liquid) {
				justify-content: center;
			}

			li {
				width: 18%;
				margin-right: 8%;
				position: relative;

				@include mq(sp, true) {
					min-width: 180px;
				}

				@include mq(sp) {
					width: 32%;
					margin-right: 2%;
				}

				&::after {
					content: "";
					display: block;
					width: 27px;
					height: 27px;
					background-image: url(../img/reform/3re/icon_plus_01.png);
					background-size: cover;
					position: absolute;
					right: -55px;
					top: 50%;
					transform: translateY( calc(-50% - 35px));

					@include mq(sp) {
						width: 14px;
						height: 14px;
						right: -10px;
						transform: translateY(40%);
					}
				}

				&:last-of-type {
					margin-right: 0;

					&::after {
						display: none;
					}
				}

				img {
					width: 100%;
				}
			}
		}
	}

	.p-page_facility {
		&__inner {
			@include mq(sp, true) {
				padding-left: 80px;
				margin-top: 60px;
			}

			@include mq(sp) {
				margin-top: 30px;
			}
		}

		.image {
			img {
				border-radius: 20px;
				overflow: hidden;
			}
		}

		&__kitchen {
			display: flex;

			@include mq(sp, true) {
				justify-content: space-between;
				margin-bottom: 60px;
			}

			@include mq(sp) {
				flex-direction: column-reverse;
			}

			.content {
				@include mq(sp, true) {
					width: 47%;
				}

				.title {
					@include rem(font-size, 2.8rem);
					margin-bottom: 20px;
					position: relative;

					@include mq(sp) {
						@include rem(font-size, 2.0rem);
						margin-bottom: 10px;
					}

					&::before {
						content: "";
						display: block;
						position: absolute;
						left: -50px;
						top: 50%;
						width: 30px;
						height: 2px;
						background-color: $color_reform;
					}
				}

				.detail {
					margin-top: 40px;
					background-color: #fff;
					padding: 30px;
					@include rem(font-size, 1.4rem);

					@include mq(sp) {
						margin-top: 20px;
						padding: 20px;
					}
				}
			}

			.image {
				@include mq(sp, true) {
					width: 48%;
				}

				@include mq(sp) {
					margin-bottom: 20px;
				}
			}
		}

		&__list {
			
			@include mq(sp, true) {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				margin-bottom: 20px;
			}

			@include mq(sp) {
				margin-bottom: 0px;
			}
		}

		&__item {
			@include mq(sp, true) {
				width: 31.2%;
			}
			@include mq(sp) {
				margin-bottom: 40px;
			}

			.image {
				margin-bottom: 20px;
			}
			.content {
				.title {
					@include rem(font-size, 2.2rem);
					margin-bottom: 10px;
				}
			}
		}
	}

	.p-page_interior {
		&__slider {
			margin-top: 60px;

			@include mq(sp) {
				margin-top: 20px;
			}

			img {
				border-radius: 20px;
				overflow: hidden;
				width: 100%;
			}
		}

		.slick-slide {
			margin: 0 20px;
		}

		.slick-dots {
			bottom: -45px;
			li {
				height: 10px;
				width: 10px;
				border-radius: 50%;
				overflow: hidden;
				button {
					height: 10px;
					width: 10px;
					background-color: #F1F1F1;
					&:before {
						width: 10px;
						height: 10px;
					}
				}
				&.slick-active button:before {
					background-color: $color_reform;
				}
			}
		}
	}

	.p-module_style {
		&__head {
			margin: 0 auto;

			@include mq(sp, true) {
				width: $width_site;
				max-width: 1080px;
			}

			@include mq(sp) {
				width: 90%;
			}
		}

		&__body {
			margin-top: 60px;

			@include mq(sp) {
				width: 90%;
				margin: 30px auto 0;
			}
		}

		&__wrap {
			position: relative;
			margin-bottom: 10px;

			&::after {
				content: "";
				display: block;
				width: 50%;
				height: 100%;
				background-color: #fff;
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				z-index: 1;
			}
		}

		&__inner {
			position: relative;
			z-index: 2;
			background-color: #fff;
			border-radius: 20px;
			margin-bottom: 10px;

			@include mq(sp, true) {
				display: flex;
				justify-content: space-between;
				align-items: center;
				width: $width_site_wide;
				max-width: 1200px;
				margin: 0 auto;
				padding: 60px;
			}

			@include mq(sp) {
				padding: 20px;
				margin-bottom: 20px;
			}
		}

		&__column {

			&__inner {
				display: flex;

				@include mq(sp, true) {
					justify-content: space-between;
					align-items: center;
				}

				@include mq(sp) {
					flex-direction: column-reverse;
				}
			}

			&__content {
				@include mq(sp, true) {
					width: 48%;
				}

				.title {
					color: $color_reform;
					position: relative;
					padding-bottom: 30px;
					margin-bottom: 30px;

					@include mq(sp) {
						padding-bottom: 20px;
						margin-bottom: 20px;
					}

					&::before {
						content: "";
						display: block;
						position: absolute;
						left: 0;
						bottom: 0;
						width: 60px;
						height: 2px;
						background-color: $color_reform;
					}
					.elem_en {
						font-family: $montserrat;
						font-weight: 700;
						@include rem(font-size, 4.2rem);
						line-height: 1.4;
						letter-spacing: 3px;

						@include mq(sp) {
							@include rem(font-size, 2.8rem);
							letter-spacing: 2px;
						}
					}

					.elem_jp {
						@include rem(font-size, 1.8rem);

						@include mq(sp) {
							@include rem(font-size, 1.6rem);
						}
					}
				}
			}

			&__image {
				@include mq(sp, true) {
					width: 46%;
				}

				@include mq(sp) {
					margin-bottom: 30px;
				}

				img {
					border-radius: 20px;
					overflow: hidden;
				}
			}
		}
	}

}
