.p-module_pagenavi {
	position: relative;
	@include mq(sp) {
		background-color: #F1F1F1;
		margin-top: 60px;
	}
	&::before {
		@include mq(sp,true) {
			content: "";
			display: block;
			width: 100%;
			height: 50%;
			background-color: #F1F1F1;
			position: absolute;
			left: 0;
			bottom: 0;
		}
	}
	&__inner {
		margin: 0 auto;
		@include mq(sp,true) {
			width: $width_site;
			max-width: 1080px;
			padding: 80px 0;
		}
		@include mq(sp) {
			width: 90%;
			padding: 20px 0 100px;
		}
	}

	&__list {
		display: flex;
		justify-content: space-between;

		@include mq(tablet) {
			flex-wrap: wrap;
		}
	}

	&__item {
		width: 24%;

		@include mq(tablet) {
			width: 48%;
			margin-top: 20px;
		}
	}
}