/*

shopinfo page

*/

body.shopinfo{
	.p-page_shopinfo {

		&__inner {
			+ .p-page_shopinfo__inner {
				margin-top: 100px;

				@include mq(sp) {
					margin-top: 60px;
				}
			}
		}

		&__detail {
			@include mq(tablet, true) {
				display: flex;
				justify-content: space-between;
			}

			.content {
				@include mq(tablet, true) {
					width: 36%;
				}

				&__inner {
					@include mq(tablet, true) {
						padding-left: 35px;
					}
				}

				address {
					font-style: normal;
				}

				.tel {
					margin-top: 10px;
					a {
						font-family: $montserrat;
						font-weight: 700;
						@include rem(font-size, 2.2rem);
					}
				}
			}

			.image_gmap {
				border-radius: 20px;
				overflow: hidden;

				@include mq(sp, true) {
					width: 60%;
					display: flex;
				}

				@include mq(tablet) {
					width: 100%;
					margin-top: 30px;
				}

				@include mq(sp) {
					display: block;
				}

				img {
					object-fit: cover;
					width: 100%;
					height: 100%;
				}

				> * {
					@include mq(sp, true) {
						width: 50%;
					}
				}
			}
		}

		&__message {
			margin-top: 40px;

			@include mq(sp) {
				margin-top: 30px;
			}

			&::before {
				@include mq(tablet, true) {
					content: "";
					display: block;
					width: 50%;
					height: 100%;
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
					background-color: #F5F5F5;
					z-index: 1;
				}
			}
			&__inner {
				margin: 0 auto;
				background-color: #F5F5F5;
				position: relative;
				z-index: 2;

				@include mq(tablet, true) {
					width: $width_site;
					max-width: 1080px;
					padding: 60px 0 60px 60px;
					border-radius: 20px 0 0 20px;
					display: flex;
					justify-content: space-between;
				}

				@include mq(tablet) {
					width: 90%;
					padding: 20px;
					border-radius: 20px;
				}
			}

			&__head {
				@include mq(tablet, true) {
					width: 24%;
				}

				.heading {
					position: relative;
					padding-bottom: 30px;
					margin-bottom: 30px;

					&::before,
					&::after {
						content: "";
						display: block;
						position: absolute;
						left: 0;
						bottom: 0;
						height: 2px;
					}

					&::before {
						background-color: $color_main;
						width: 50px;
						z-index: 2;
					}

					&::after {
						background-color: #E6E6E6;
						width: 196px;
						z-index: 1;
					}

					.elem_en {
						@include rem(font-size, 3.6rem);
						color: $color_main;
						font-family: $montserrat;
						font-weight: 700;
						line-height: 1.4;
					}

					.elem_jp {
						@include rem(font-size, 1.8rem);
					}
				}

				.manager {
					dt {
						@include rem(font-size, 1.4rem);
					}

					dd {
						@include rem(font-size, 2.0rem);
					}
				}
			}

			&__body {
				@include mq(tablet, true) {
					width: 70%;
				}

				.title {
					@include rem(font-size, 2.2rem);

					@include mq(tablet) {
						@include rem(font-size, 2.0rem);
						margin-bottom: 10px;
					}
				}
			}
		}

	}
}
