/*

insurance page

*/

body.insurance{
	.p-page_risk {

		&__body {
			position: relative;
			margin-top: 40px;
			&::after {
				@include mq(tablet, true) {
					content: "";
					display: block;
					width: 50%;
					height: 100%;
					background-color: #fff;
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
					z-index: 1;
					border-radius: 20px 0 0 20px;
				}
			}
		}

		&__wrap {
			background-color: #fff;
			margin: 0 auto;

			@include mq(tablet, true) {
				border-radius: 0 20px 20px 0;
				width: $width_site_wide;
				max-width: 1200px;
				padding: 60px;
			}

			@include mq(tablet) {
				padding: 60px;
			}

			@include mq(sp) {
				padding: 20px;
			}

			&.bg--gray {
				background-color: #F5F5F5;
			}
		}

		&__inner {
			position: relative;
			z-index: 2;

			@include mq(tablet, true) {
				display: flex;
				justify-content: space-between;
			}
		}

		&__column {
			width: 100%;

			&__inner {
				@include mq(tablet, true) {
					display: flex;
					justify-content: space-between;
				}
			}

			&__title {
				@include rem(font-size, 2.8rem);

				@include mq(tablet, true) {
					width: 34%;
				}
				@include mq(tablet) {
					@include rem(font-size, 2.0rem);
					margin-bottom: 20px;
				}
			}

			&__content {
				@include mq(tablet, true) {
					width: 62%;
				}
			}
		}
	}
}
