.c-module_insurance {

	&__head {
		margin: 0 auto;

		@include mq(tablet, true) {
			width: $width_site;
			max-width: 1080px;
		}

		;
		@include mq(tablet) {
			width: 90%;
		}
	}

	&__body {
		position: relative;
		margin-top: 40px;
		&::after {
			@include mq(tablet, true) {
				content: "";
				display: block;
				width: 50%;
				height: 100%;
				background-color: #fff;
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				z-index: 1;
			}
		}

		&.bg--gray {
			&::after {
				@include mq(tablet, true) {
					background-color: #F5F5F5;
				}
			}
		}
	}

	&__wrap {
		background-color: #fff;
		margin: 0 auto;

		@include mq(tablet, true) {
			padding: 60px;
			width: $width_site_wide;
			max-width: 1200px;
			border-radius: 20px 0 0 20px;
		}

		@include mq(tablet) {
			padding: 30px;
		}

		@include mq(sp) {
			padding: 20px;
			width: 90%;
		}

		&.bg--gray {
			background-color: #F5F5F5;
		}
	}

	&__inner {
		position: relative;
		z-index: 2;
		border-bottom: 1px solid $color_line;

		@include mq(sp, true) {
			display: flex;
			justify-content: space-between;
			padding-bottom: 60px;
		}

		@include mq(sp) {
			margin-bottom: 30px;
			padding-bottom: 30px;
		}

		+ .c-module_insurance__inner {
			@include mq(sp, true) {
				padding-top: 60px;
			}
		}

		&:last-of-type {
			border-bottom: none;
			padding-bottom: 0;
		}
	}

	&__column {

		&__inner {

			@include mq(sp, true) {
				display: flex;
				justify-content: space-between;
			}

			+ .c-module_insurance__column__inner {
				padding-top: 40px;
				border-top: 1px dashed $color_line;
				padding-bottom: 0;
			}
		}

		&__content {
			@include mq(sp, true) {
				width: 62%;
				padding-left: 35px;
			}
		}

		&__image {
			@include mq(sp, true) {
				width: 29%;
			}

			@include mq(sp) {
				margin-top: 20px;
			}

			img {
				border-radius: 20px;
				overflow: hidden;
			}
		}
	}


}