/*

download page

*/

body.download{

	.p-page_download {
		@include mq(sp) {
			width: 100%;
			margin-top: 40px;
			overflow-x: auto;
			-webkit-overflow-scrolling: touch;
		}
		.p-btn {
			min-width: 90px;
			max-width: 90px;
			margin: 0 auto;
			a {
				padding: 8px 10px;
				font-family: $montserrat;
				@include rem(font-size, 1.4rem);
			}
		}
	}

	.p-page_videos {
		&__list {
			@include mq(sp, true) {
				display: flex;
				flex-wrap: wrap;
			}
		}

		&__item {
			@include mq(sp, true) {
				width: 32%;
				margin-right: 2%;
				margin-bottom: 2%;
			}

			@include mq(tablet) {
				width: 48%;
			}

			@include mq(sp) {
				width: 100%;
				margin-bottom: 30px;
			}

			&:nth-child(3n) {
				@include mq(tablet, true) {
					margin-right: 0;
				}
			}
			iframe {
				width: 100%;
				vertical-align: top;

				@include mq(sp) {
					height: 200px;
				}
			}
		}
	}

}
