/*

case page

*/

body.case{

	.p-page_case {
		&__list {
			@include mq(sp, true) {
				display: flex;
				flex-wrap: wrap;
			}
		}

		&__item {
			@include mq(sp, true) {
				width: 31%;
				margin-right: 3%;
				margin-bottom: 3%;
			}

			@include mq(sp) {
				margin-bottom: 30px;
			}

			&:nth-child(3n) {
				margin-right: 0;
			}

			a {
				display: block;
				&:hover {
					text-decoration: none;
					.title {
						color: $color_reform;
					}
				}
			}

			.thumb {
				img {
					border-radius: 10px;
					overflow: hidden;
				}
			}

			.content {
				position: relative;
				padding-top: 20px;
			}

			.category {
				background-color: $color_reform;
				color: #fff;
				@include rem(font-size, 1.2rem);
				border-radius: 0 100px 100px 0;
				padding: 2px 10px;
				position: absolute;
				top: -13px;
				left: 0;
			}

			.title {
				@include rem(font-size, 1.8rem);
				font-weight: 600;
				line-height: 1.5;
				transition: color .3s;
			}

			.date {
				@include rem(font-size, 1.4rem);
				font-family: $montserrat;
				color: #959595;
			}
		}


	}

	.p-post_detail {
		&__head {
			border-bottom: 1px solid $color_line;
			padding-bottom: 40px;
			margin-bottom: 40px;

			@include mq(sp) {
				padding-bottom: 20px;
				margin-bottom: 20px;
			}

			.category {
				margin-bottom: 20px;
				a {
					background-color: $color_reform;
					color: #fff;
					text-align: center;
					@include rem(font-size, 1.4rem);
					padding: 2px 30px;
					border-radius: 100px;
					display: inline-block;
					border: $color_main 2px solid;
					transition: color .2s, background-color .2s;
					min-width: 120px;

					@include mq(sp) {
						padding: 0 10px;
					}

					&:hover {
						text-decoration: none;
					}
				}

			}

			.title {
				@include rem(font-size, 3.6rem);
				margin-bottom: 20px;
				font-weight: 600;
				line-height: 1.5;

				@include mq(sp) {
					@include rem(font-size, 2.4rem);
				}

				span {
					display: block;
					overflow: hidden;
				}
			}

			.meta {
				.date {
					@include rem(font-size, 1.4rem);
					font-family: $montserrat;
					color: #959595;
				}
			}
		}

		&__content {

			h2 {
				@include rem(font-size, 2.8rem);
				line-height: 2.2;
				position: relative;
				padding: 10px 20px;
				background-color: #F5F5F5;
				border-left: 4px solid $color_reform;

				@include mq(sp) {
					@include rem(font-size, 2.0rem);
					padding: 5px 15px;
				}
			}
			h3 {
				@include rem(font-size, 2.2rem);
				line-height: 2.0;
				border-bottom: 2px solid $color_reform;

				@include mq(sp) {
					@include rem(font-size, 1.8rem);
				}
			}
			h4 {
				@include rem(font-size, 1.8rem);
				line-height: 1.8;

				@include mq(sp) {
					@include rem(font-size, 1.6rem);
				}

				&::before {
					content: "-";
					display: inline-block;
					vertical-align: middle;
					margin-right: 10px;
				}
			}
			h5 {
				@include rem(font-size, 1.6rem);
				line-height: 1.6;
			}
			h6 {
				@include rem(font-size, 1.4rem);
				line-height: 1.4;
			}
		}
	}

}
