.p-radio {
	display: flex;
	justify-content: space-between;
	margin: 0 auto;

	@include mq(sp, true) {
		max-width: 1080px;
		width: 100%;
		position: absolute;
		top: -33px;
		left: 50%;
		transform: translateX(-50%);
	}

	@include mq(sp) {
		flex-wrap: wrap;
		width: 100%;
	}

	> * {
		text-align: center;
		width: 100%;
		margin: 0 1%;

		@include mq(sp) {
			width: 48%;
			margin-bottom: 20px;
		}

		a,
		label {
			@include rem(font-size, 2.2rem);
			background-color: #fff;
			border-radius: 100px;
			border: $color_main 3px solid;
			padding: 10px;
			display: block;
			position: relative;
			transition: background-color .3s;
			cursor: pointer;

			@include mq(tablet) {
				@include rem(font-size, 1.6rem);
			}

			&::before {
				position: absolute;
				left: 50%;
				bottom: -14px;
				z-index: 0;
				content: "";
				display: block;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 0px 9px 0 9px;
				border-color: $color_main transparent transparent transparent;
				transform: translateX(-50%);
				transform-origin: center top;
				transition: border-width .1s;
				opacity: 0;
			}
		}

		input {
			display: none;

			&:checked {
				+ label {
					background-color: $color_main;
					color: #fff;

					&::before {
						border-width: 14px 9px 0 9px;
						opacity: 1;
					}
				}
			}
		}

		a {
			&:hover {
				text-decoration: none;

				&::before {
					border-width: 14px 9px 0 9px;
					opacity: 1;
				}
			}
		}
	}

	&--karitai {
		input {
			&:checked {
				+ label {
					background-color: $color_karitai;
				}
			}
		}
		label {
			border-color: $color_karitai;
			color: $color_karitai;

			&::before {
				border-color: $color_karitai transparent transparent transparent;
			}
		}
		a {
			border-color: $color_karitai;
			color: $color_karitai;
			&:hover {
				background-color: $color_karitai;
				color: #fff;

				&::before {
					border-color: $color_karitai transparent transparent transparent;
				}
			}
		}
	}

	&--kaitai {
		input {
			&:checked {
				+ label {
					background-color: $color_kaitai;
				}
			}
		}
		label {
			border-color: $color_kaitai;
			color: $color_kaitai;

			&::before {
				border-color: $color_kaitai transparent transparent transparent;
			}
		}
		a {
			border-color: $color_kaitai;
			color: $color_kaitai;
			&:hover {
				background-color: $color_kaitai;
				color: #fff;

				&::before {
					border-color: $color_kaitai transparent transparent transparent;
				}
			}
		}
	}

	&--uritai {
		input {
			&:checked {
				+ label {
					background-color: $color_uritai;
				}
			}
		}
		label {
			border-color: $color_uritai;
			color: $color_uritai;

			&::before {
				border-color: $color_uritai transparent transparent transparent;
			}
		}
		a {
			border-color: $color_uritai;
			color: $color_uritai;
			&:hover {
				background-color: $color_uritai;
				color: #fff;

				&::before {
					border-color: $color_uritai transparent transparent transparent;
				}
			}
		}
	}

	&--reform {
		input {
			&:checked {
				+ label {
					background-color: $color_reform;
				}
			}
		}
		label {
			border-color: $color_reform;
			color: $color_reform;

			&::before {
				border-color: $color_reform transparent transparent transparent;
			}
		}
		a {
			border-color: $color_reform;
			color: $color_reform;
			&:hover {
				background-color: $color_reform;
				color: #fff;

				&::before {
					border-color: $color_reform transparent transparent transparent;
				}
			}
		}
	}
}