.p-module_voice {
	position: relative;

	@include mq(tablet, true) {
		width: $width_site_wide;
		max-width: 1200px;
		background-image: url(../img/common/bg_voice_01.jpg);
		padding: 50px 60px 80px;
		margin: 0 auto;
		border-radius: 20px;
	}

	@include mq(tablet) {
		background-image: url(../img/common/bg_voice_01_sp.jpg);
		padding: 30px 40px 60px;
		background-repeat: no-repeat;
		background-size: cover;
	}

	@include mq(sp) {
		padding: 30px 20px 60px;
	}

	&__head {
		color: #fff;
		margin-bottom: 4%;

		@include mq(sp, true) {
			display: flex;
			align-items: center;
		}

		@include mq(sp) {
			text-align: center;
			margin-bottom: 40px;
		}

		.elem_en {
			@include rem(font-size, 7.0rem);
			font-family: $montserrat;
			line-height: 1.3;

			@include mq(sp, true) {
				border-right: 2px solid #fff;
				padding-right: 25px;
				margin-right: 25px;
			}

			@include mq(sp) {
				@include rem(font-size, 5.0rem);
			}
		}

		.elem_jp {
			@include rem(font-size, 1.8rem);
			letter-spacing: 2px;
		}
	}

	&__list {
		@include mq(sp, true) {
			display: flex;
			justify-content: space-between;
		}

		@include mq(tablet) {
			flex-wrap: wrap;
		}
	}

	&__item {
		@include mq(tablet) {
			width: 48%;
		}

		@include mq(sp) {
			width: 100%;
		}
	}
	.p-btn {

		@include mq(tablet, true) {
			position: absolute;
			right: 60px;
			bottom: -35px;
		}

		@include mq(tablet) {
			margin: 50px auto 0;
		}
	}
}