.c-module_banner {
	margin-top: 2%;
	position: relative;

	&::before {
		content: "";
		display: block;
		background-color: #F1F1F1;
		width: 100%;
		height: 50%;
		position: absolute;
		left: 0;
		bottom: 0;
		z-index: 1;
	}
	&__inner {
		position: relative;
		z-index: 2;
	}
	&__list {
		@include mq(sp, true) {
			display: flex;
			justify-content: space-between;
		}
	}
	&__item {
		text-align: center;
		border-radius: 20px;
		overflow: hidden;
		background-size: cover;

		@include mq(sp, true) {
			width: 49%;
		}

		@include mq(sp) {
			margin-bottom: 20px;
		}

		&:last-of-type {
			@include mq(sp) {
				margin-bottom: 0;
			}
		}

		a {
			display: block;
			color: #fff;
			position: relative;
			border: #f1f1f1 6px solid;
			transition: border-color .3s;
			overflow: hidden;
			border-radius: 20px;
			background-color: $color_text;

			&:hover {
				text-decoration: none;
				border-color: $color_main;

				img {
					opacity: 0.8;
				}
			}

			figure {
				overflow: hidden;
			}

			.title {
				font-weight: 600;
				@include rem(font-size, 2.0rem);
				position: absolute;
				left: 50%;
				top: 50%;
				width: 100%;
				transform: translate(-50%,-50%);

				&::before {
					@include rem(font-size, 4.6rem);
					display: block;
					margin-bottom: 15px;
					font-weight: 400;
				}
			}

			img {
				opacity: 0.6;
				transition: opacity .3s;
				border-radius: 15px;
				overflow: hidden;
			}
		}
	}
}