.p-pager {
	margin-top: 50px;

	@include mq(sp) {
		margin-top: 30px;
	}

	&__list {
		display: flex;
		justify-content: space-between;
		width: 100%;
		border: #E6E6E6 1px solid;
		border-radius: 100px;
		overflow: hidden;

		> * {
			width: 33.333%;

			a {
				color: $color_main;
				line-height: 1;
				text-align: center;
				display: block;
				padding: 40px 0;
				position: relative;
				transition: background-color .2s, color .2s;

				@include mq(sp) {
					padding: 20px 0;
					@include rem(font-size, 1.2rem);
				}

				i {
					transition: color .2s;
				}

				span {
					@include mq(sp) {
						display: none;
					}
				}

				&:hover {
					text-decoration: none;
					background-color: $color_main;
					color: #fff;

					i {
						color: #fff;
					}
				}
			}
		}
	}

	&__prev,
	&__next {
		@include mq(sp) {
			width: auto;
		}
		&.disable {
			a {
				color: #E6E6E6;
				pointer-events: none;
			}
		}
		a {
			@include mq(sp) {
				display: block;
				height: 100%;
			}
			i {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);

				@include mq(sp) {
					@include rem(font-size, 2.0rem);
				}
			}
		}
	}

	&__next {
		a {
			i {
				right: 30px;

				@include mq(sp) {
					right: 20px;
				}
			}
		}
	}

	&__prev {
		a {
			i {
				left: 30px;

				@include mq(sp) {
					left: 20px;
				}
			}
		}
	}

	&__item {
		@include mq(sp) {
			width: auto;
		}

		a {
			@include mq(sp) {
				padding-right:20px;
				padding-left: 20px;
				@include rem(font-size, 1.4rem);
			}

			&::before,
			&::after {
				content: "";
				display: block;
				width: 1px;
				height: 37px;
				background-color: $color_main;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
			}

			&::before {
				left: 0;
			}

			&::after {
				right: 0;
			}
		}
	}
}