章番号用mixin (IE8以上)

キャプションに章番号をつける場合
```sass
$counterName: oreoreCounter;
.parent-section {
	@include resetCounter($counterName);
	h2 {
		@include addCounter($counterName, '第', '章');
	}
}
```

入れ子になってるリストに通し番号(1-1-1など）を付ける場合
```sass
$counterName: listCounter;
ol {
	@include resetCounter($counterName);
	li {
		@include addCounters($counterName, '-');
	}
}
```

