.list_disc > li, .list_square > li, .list_decimal > li, .list_decimal_zero > li, .list_dot > li, .list_circle > li, .list_decimal_01 > li, .list_decimal_02 > li, .list_decimal_03 > li, .list_decimal_04 > li, .list_upper_alpha_01 > li {
