.just_layout {
  text-align: justify;
  text-justify: distribute-all-lines;
  zoom: 1;
  display: block;
  line-height: 0; }
  .just_layout:after {
    line-height: 0;
    visibility: hidden;
    content: "";
    display: inline-block;
    width: 100%; }
  .just_layout__item {
    display: inline-block;
    text-align: left;
    vertical-align: top;
    line-height: normal; }

* html .just_layout .just_layout__item {
  display: inline;
  zoom: 1; }

*:first-child + html .just_layout .just_layout__item {
  display: inline;
  zoom: 1; }

