.l-site_header{
	width: 100%;
	z-index: 100;
	padding: 10px 0;
	background-color: $color_main;

	&__inner {
		margin: 0 auto;

		@include mq(sp,true) {
			max-width: 1366px;
			width: 100%;
			margin: 0 auto;
			display: flex;
			justify-content: space-between;
			align-items: flex-end;
		}

		@include mq(liquid) {
			width: 90%;
		}
	}

	&__id {
		transition: opacity .3s, margin-left .3s;

		@media screen and (max-width: 1560px) {
			margin-left: 100px;
		}

		@include mq(tablet) {
			margin-left: 60px;
		}

		a {
			display: flex;
			justify-content: space-between;
			transition: opacity .3s;

			@include mq(sp) {
				justify-content: flex-start;
			}

			&:hover {
				opacity: 0.6;
				text-decoration: none;
			}
		}

		.logo {
			margin-right: 20px;

			@include mq(sp) {
				width: 85px;
			}
		}

		.elem {
			color: #fff;
			text-align: center;
			&_jp {
				font-weight: 400;
				margin-bottom: 5px;

				@include mq(sp) {
					@include rem(font-size, 1.2rem);
					line-height: 1.3;
				}
			}

			&_en {
				font-family: $montserrat;
				font-weight: 700;
				@include rem(font-size, 1.2rem);
				border: #fff 2px solid;

				@include mq(sp) {
					@include rem(font-size, 1.0rem);
					border-width: 1px;
				}
			}
		}
	}

	&__nav {
		@include mq(tablet) {
			display: none;
		}
		&__list {
			display: flex;
			justify-content: space-between;

			@include mq(sp) {
				justify-content: center;
			}
		}

		&__item {
			text-align: center;
			@include mq(sp, true) {
				min-width: 150px;
				margin-left: 10px;
				position: relative;
				top: 10px;
			}
			@include mq(sp) {
				width: 100%;
			}

			&.request {
				a {
					background-color: $color_accent;
					color: $color_text;
					border-color: $color_accent;

					&:hover {
						color: $color_accent
					}
				}
			}
			a {
				padding: 10px 20px;
				border-radius: 100px;
				background-color: #fff;
				color: $color_main;
				display: block;
				transition: background-color .3s;
				@include rem(font-size, 1.4rem);
				line-height: 1;

				@include mq(sp, true) {
					border: #fff 2px solid;
				}

				@include mq(sp) {
					border-radius: 0;
					background-color: $color_main;
					color: #fff;
					letter-spacing: 0;
					padding: 10px 0;
					border-left: #fff 1px solid;
				}

				&:hover {
					text-decoration: none;
					background-color: $color_main;
					color: #fff;

					&::before {
						transform: scaleX(1);
						transform-origin: left center;
					}
				}

				&::before {
					display: block;
					@include rem(font-size, 2.0rem);
					margin-bottom: 7px;
				}

				> span {
					display: none;

					@include mq(sp) {
						@include rem(font-size, 1.0rem);
						display: inline-block;
						vertical-align: bottom;
						margin-left: 17px;
						position: relative;
						top: -1px;
					}
				}
			}
		}
	}

	&__menu {
		position: fixed;
		left: 0;
		top: 0;
		cursor: pointer;
		z-index: 200;
		display: block;
		background-color: #0B4B95;
		width: 83px;
		height: 83px;
		border: none;
		-webkit-appearance: none;

		@include mq(sp) {
			width: 60px;
			height: 60px;
		}

		&__inner {
			width: 34px;
			height: 23px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);

			@include mq(sp) {
				width: 25px;
				height: 14px;
			}
		}

		span {
			display: block;
			width: 100%;
			height: 2px;
			background-color: #fff;
			position: absolute;
			left: 0;
			transition: transform .3s, opacity .3s, width .3s;
			transform-origin: left center;

			@include mq(sp) {
				height: 1px;
			}

			&:first-child {
				top: 0;
				transform-origin: right center;
			}

			&:nth-child(2) {
				width: 27px;
				top: 50%;
				transform-origin: right center;
				transform: scaleX(1) translateY(-50%);

				@include mq(sp) {
					width: 21px;
				}
			}

			&:nth-child(3) {
				width: 17px;
				bottom: 0;
				transform-origin: right center;
			}

			&:nth-child(4) {
				transform: scaleX(1.3) rotateZ(40deg);
				transform-origin: left top;
				width: 0;


			}

			&:nth-child(5) {
				transform: scaleX(1.3) rotateZ(-40deg);
				transform-origin: left bottom;
				bottom: 0;
				width: 0;

			}

		}
	}

	&__gnav {
		position: fixed;
		left: 0;
		top: 0;
		z-index: 100;
		background-color: #F5F5F5;
		display: none;
		overflow-y: auto;

		@include mq(tablet, true) {
			width: 45%;
			//height: 90vh;
		}

		@include mq(tablet) {
			width: 80%;
		}

		@include mq(sp) {
			width: 100%;
			//height: calc(100vh - 54px);
			height: 100vh;
			-webkit-overflow-scrolling: touch;
		}

		&__inner {
			overflow-y: auto;
		}

		&__head {
			transition: opacity .3s;
			padding-left: 83px;

			a {
				display: flex;
				transition: opacity .3s;
				padding: 10px;

				@include mq(sp) {
					padding: 10px 0;
				}

				&:hover {
					opacity: 0.6;
					text-decoration: none;
				}
			}

			.logo {
				margin-right: 20px;

				@include mq(sp) {
					width: 85px;
				}
			}

			.elem {
				color: $color_main;
				text-align: center;
				&_jp {
					font-weight: 400;
					margin-bottom: 5px;

					@include mq(sp) {
						@include rem(font-size, 1.2rem);
						line-height: 1.3;
					}
				}

				&_en {
					font-family: $montserrat;
					font-weight: 700;
					@include rem(font-size, 1.2rem);
					border: $color_main 2px solid;

					@include mq(sp) {
						@include rem(font-size, 1.0rem);
						border-width: 1px;
					}
				}
			}
		}

		&__inner {
			@include mq(sp, true) {
				padding: 5% 12% 8%;
			}

			@include mq(sp) {
				padding: 10px 30px 40px;
			}
		}

		nav {
			> ul {
				display: flex;
				flex-wrap: wrap;

				> li {
					width: 48%;
					margin-right: 4%;
					border-bottom: 1px solid #E6E6E6;

					&:nth-child(2n) {
						margin-right: 0;
					}

					&:last-of-type {
						@include mq(sp) {
							width: 100%;
							display: flex;
							align-items: flex-start;
						}

						ul {
							@include mq(sp) {
								padding-left: 20px;
								margin-top: 20px;
								margin-bottom: 10px;
							}
						}
					}
					> a {
						display: block;
						padding: 20px 0;
						@include rem(font-size, 2.0rem);

						@include mq(sp) {
							@include rem(font-size, 1.6rem);
						}

						&::before {
							width: 30px;
							height: 30px;
							line-height: 30px;
							display: inline-block;
							border-radius: 50%;
							background-color: $color_main;
							color: #fff;
							text-align: center;
							@include rem(font-size, 1.7rem);
							margin-right: 10px;
						}

						&:hover {
							color: $color_main;
							text-decoration: none;
						}

						&.p-icon{
							&--karitai {
								&::before {
									background-color: $color_karitai;
								}
							}
							&--kaitai {
								&::before {
									background-color: $color_kaitai;
								}
							}
							&--kashitai {
								&::before {
									background-color: $color_kashitai;
								}
							}
							&--uritai {
								&::before {
									background-color: $color_uritai;
								}
							}
							&--reform {
								&::before {
									background-color: $color_reform;
								}
							}
						}
					}

					> ul {
						padding-left: 40px;
						margin-top: -10px;
						margin-bottom: 15px;

						a {
							@include rem(font-size, 1.5rem);
							display: block;
							transition: padding-left .2s;

							@include mq(sp) {
								@include rem(font-size, 1.4rem);
							}

							&:hover {
								padding-left: 10px;
								text-decoration: none;
							}

							&::before {
								content: "-";
								display: inline-block;
								vertical-align: middle;
								margin-right: 5px;
							}
						}
					}
				}
			}
		}

		aside {
			margin-top: 30px;

			@include mq(sp) {
				margin-top: 20px;
			}

			> ul {
				display: flex;
				flex-wrap: wrap;
				> li {
					width: 48%;
					margin-right: 4%;
					@include mq(sp) {
						width: 49%;
						margin-right: 2%;
					}
					&:nth-child(2n) {
						margin-right: 0;
					}
					> a {
						display: block;
						@include rem(font-size, 1.5rem);
						display: block;
						padding: 10px 0;

						@include mq(sp) {
							@include rem(font-size, 1.4rem);
							padding: 7px 0;
							letter-spacing: 0;
						}

						&::before {
							content: "-";
							display: inline-block;
							vertical-align: middle;
							margin-right: 5px;
						}
					}
				}
			}
		}

		.p-btn {
			margin-top: 50px;

			@include mq(sp) {
				margin-top: 30px;
			}
		}

		.l-site_header__nav__list {
			display: none;

			@include mq(sp) {
				display: flex;
			}
		}
	}
}

html.is_menu_active {
	.l-site_header__menu {
		span {
			&:first-child {
				transform: scale(0);
			}

			&:nth-child(2) {
				transform: scaleX(0);
			}

			&:nth-child(3) {
				transform: scaleX(0);
			}

			&:nth-child(4) {
				transform: scaleX(1.3) rotateZ(40deg);
				width: 100%;

				@include mq(sp) {
					transform: scaleX(1.3) rotateZ(35deg);
				}
			}

			&:nth-child(5) {
				transform: scaleX(1.3) rotateZ(-40deg);
				width: 100%;

				@include mq(sp) {
					transform: scaleX(1.3) rotateZ(-35deg);
				}
			}
		}
	}
}